<template>
  <base-button
    @click.native="handleDeleteOne(id)"
    class="remove btn-link btn-icon-only text-white "
    type="danger"
    size="sm"
    icon
    :loading="loading"
  >
    <i v-show="!loading" class="text-white fa fa-trash"></i>
  </base-button>
</template>

<script>
  export default {
    name: "DeckDeleteButton",
    props: {
      id: Number,
      title: String
    },
    data () {
      return {
        loading: false
      }
    },
    methods: {
      async handleDelete(id) {
        await this.axios.delete(process.env.VUE_APP_ROOT_API + '/api/v1/vocabulary/decks/' + id)
          .then(() => this.$notify({type: 'warning', message: `Deleted ${this.title} deck`}))
          .catch(() => this.$notify({type: 'danger', message: `Failed to delete ${this.title} deck from your vocabulary. If this happens again, please reach out to support@chinesepod.com`}))
      },
      async handleDeleteOne(row) {
        this.loading = true;
        await this.handleDelete(row);
        this.$emit('deleted');
        this.loading = false
      }
    }
  }
</script>

