<template>
  <div>
    <div class="header pt-6 pb-6 d-flex align-items-center profile-header">
      <!-- Mask -->
      <span class="mask bg-gradient-default opacity-8"></span>
      <!-- Header container -->
      <div class="container-fluid d-flex align-items-center">
        <div class="row">
          <div class="col-lg-7 col-md-10">
            <h1 class="display-2 text-white">Hello {{ userInfo.username }}</h1>
            <p class="text-white mt-0 mb-5">This is your <strong>Subscription</strong> page. You can update your payment information here, retrieve receipts, renew subscription or stop automatic renewal of an existing subscription.</p>
          </div>
        </div>
      </div>
    </div>

    <div class="container-fluid mt--4 mt-xl--6">
      <div class="row">
        <div class="col-xl-4 order-xl-2">
          <user-card :user-info="userInfo"></user-card>
        </div>
        <div class="col-xl-8 order-xl-1">
          <subscription-info :loading="loadingSubscriptions" :subscription-data="subscriptionInfo" @updated="getSubscriptionInfo"/>
          <transaction-info :loading="loadingTransactions" :transaction-data="transactionInfo" @updated="getTransactionInfo"/>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import {mapState} from 'vuex';

  import UserCard from './UserProfile/UserCard';
  import SubscriptionInfo from "./UserProfile/SubscriptionInfo";
  import TransactionInfo from "./UserProfile/TransactionInfo";
  import UpdateCard from './UserProfile/UpdateCard'

  export default {
    title: 'Subscription | ChinesePod',
    components: {
      TransactionInfo,
      SubscriptionInfo,
      UserCard
    },
    data () {
      return {
        loading: true,
        loadingSubscriptions: true,
        loadingTransactions: true,
        modal: false,
        subscriptionInfo: [],
        transactionInfo: [],
        subscriptionId: ''
      }
    },
    computed: {
      ...mapState({
        userInfo: 'userInfo'
      })
    },
    async beforeMount() {
      this.loading = true;
      await Promise.all([
        this.getSubscriptionInfo(),
        this.getTransactionInfo()
      ]);
      this.loading = false
    },
    methods: {
      async getSubscriptionInfo () {
        this.loadingSubscriptions = true;
        await this.axios.get(process.env.VUE_APP_ROOT_API + "/api/v1/account/subscription/subscriptions")
          .then(({data}) => this.subscriptionInfo = data)
          .catch(() => {});
        this.loadingSubscriptions = false;
      },
      async getTransactionInfo () {
        this.loadingTransactions = true;
        await this.axios.get(process.env.VUE_APP_ROOT_API + "/api/v1/account/subscription/transactions")
          .then(({data}) => this.transactionInfo = data)
          .catch(() => {});
        this.loadingTransactions = false;
      }
    }
  };
</script>
