<template>
  <div class="card card-profile">
    <!--    <img src="img/theme/img-1-1000x600.jpg" alt="Image placeholder" class="card-img-top">-->

    <div class="row justify-content-center">
      <div class="col-lg-3 order-lg-2">
        <div class="card-profile-image">
          <a href="#" @click="modal = true" title="Change" class="change-avatar">
            <div class="card m-0">
              <div class="card-img">
                <img :src="userInfo.userAvatar" class="rounded-circle bg-white">
              </div>
              <div class="card-img-overlay p-0 text-center">
                <i class="fa fa-user-edit text-white edit-icon"></i>
              </div>
            </div>

          </a>
        </div>
      </div>
    </div>

    <div class="card-header text-center border-0 pt-5 pt-md-4 pb-0 pb-md-4">
      <div class="d-flex justify-content-between">
        <!--        <a href="#" class="btn btn-sm btn-info mr-4">Connect</a>-->
        <!--        <a href="#" class="btn btn-sm btn-default float-right">Message</a>-->
      </div>
    </div>

    <div class="card-body pt-4">
      <div class="text-center">
        <h5 v-if="userInfo && userInfo.name" class="h3">
          {{ userInfo.name }}<span class="font-weight-light"> </span>
        </h5>
        <div v-if="userInfo && userInfo.level" class="h5 font-weight-300">
          <i class="mr-2 ni ni-hat-3"></i>{{levelToTitle(userInfo.level).toUpperCase()}}
        </div>
        <div v-if="userInfo && userInfo.access" class="h5 mt-4">
          Access: {{userInfo.access.toUpperCase()}}
        </div>
        <div v-if="showExpiry" class="h5">
          <i class="ni mr-2"></i>Expires: {{expiryDate}}
        </div>
        <div class="controls">
          <div v-if="userInfo.access !== 'premium'"  class="col-8 offset-2 col-md-6 offset-md-3 pb-2">
            <a  href="/upgrade" class="mr-2">
              <base-button size="sm" block type="success">Upgrade</base-button>
            </a>
          </div>
          <div class="col-8 offset-2 col-md-6 offset-md-3 pb-2">
            <router-link to="/profile">
              <base-button size="sm" block type="secondary">Profile</base-button>
            </router-link>
          </div>
          <div class="col-8 offset-2 col-md-6 offset-md-3 pb-2">
            <router-link to="/settings">
              <base-button size="sm" block type="secondary">Settings</base-button>
            </router-link>
          </div>
          <div class="col-8 offset-2 col-md-6 offset-md-3 pb-2">
            <router-link to="/subscription">
              <base-button size="sm" block type="secondary">Subscription</base-button>
            </router-link>
<!--            <a href="https://chinesepod.com/accounts/payments" title="Billing Section">-->
<!--              <base-button size="sm" block type="secondary">Subscription</base-button>-->
<!--            </a>-->
          </div>
        </div>
      </div>
    </div>
    <modal :show.sync="modal">
      <template slot="header">
        <h5 class="modal-title" id="avatarModal">Upload Avatar Picture</h5>
      </template>
      <div>
        <div class="text-center">
          <div class="row">
            <div class="col-12">
              <img v-if="userInfo && userInfo.userAvatar" :src="userInfo.userAvatar" class="img img-fluid">
            </div>
            <div class="col-12 pt-2">
              <base-button id="pick-avatar" type="secondary">Replace Image</base-button>
            </div>
          </div>
          <avatar-cropper
            @uploaded="handleUploaded"
            trigger="#pick-avatar"
            :labels="labels"
            :upload-url="uploadUrl" />
        </div>
      </div>
      <template slot="footer">
        <base-button type="default" @click="modal = false">Close</base-button>
      </template>
    </modal>
  </div>
</template>
<script>
  import moment from 'moment';
  import AvatarCropper from "vue-avatar-cropper"

  export default {
    data () {
      return {
        modal: false,
        labels: {
          submit: 'Update',
          cancel: 'Cancel'
        }
      }
    },
    components: {AvatarCropper},
    props: {
      userInfo: Object
    },
    computed: {
      showExpiry () {
        return this.userInfo.expiry && ['basic', 'premium'].includes(this.userInfo.access)
      },
      expiryDate () {
        return moment(this.userInfo.expiry).locale('us').format("dddd, MMMM Do YYYY")
      },
      uploadUrl () {
        return process.env.VUE_APP_ROOT_API + '/api/v1/account/upload-avatar'
      }
    },
    methods: {
      async handleUploaded () {
        this.modal = false;
        await this.$store.dispatch('getUserInfo');
      },
      levelToTitle (string) {
        const map = {
          newbie: 'Newbie',
          elementary: 'Elementary',
          preint: 'Pre Intermediate',
          intermediate: 'Intermediate',
          upperint: 'Upper Intermediate',
          advanced: 'Advanced',
          media: 'Media'
        };
        return map[string.toLowerCase()]
      }
    }
  };
</script>
<style>
  .edit-icon {
    font-size: 2rem !important;
    color: black;
    -webkit-text-fill-color: white;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: black;
  }
</style>
