<script>
  /* eslint-disable no-new */
  import PerfectScrollbar from 'perfect-scrollbar';
  import 'perfect-scrollbar/css/perfect-scrollbar.css';

  function hasElement(className) {
    return document.getElementsByClassName(className).length > 0;
  }

  function initScrollbar(className) {
    if (hasElement(className)) {
      new PerfectScrollbar(`.${className}`);
    } else {
      // try to init it later in case this component is loaded async
      setTimeout(() => {
        initScrollbar(className);
      }, 100);
    }
  }

  import DashboardNavbar from './DashboardNavbar.vue';
  import ContentFooter from './ContentFooter.vue';
  import { FadeTransition } from 'vue2-transitions';
  import Upgrade from "../../components/Lesson/Upgrade";
  import store from '@/store'

  export default {
    beforeRouteEnter: (to, from, next) => {
      // store.dispatch('getToken').then(() => store.dispatch('getUserInfo').then(() => next()).catch(() => window.location.href = '/login')).catch(() => window.location.href = '/login')
      store.dispatch('getToken')
        .then(() => next())
        .catch(() => {
          store.commit('RESET_STATE');
          window.location.href = '/login'
        })
    },
    components: {
      Upgrade,
      DashboardNavbar,
      ContentFooter,
      FadeTransition
    },
    title () { return 'Dashboard | ChinesePod'},
    computed: {
      banner() {return this.$store.state.userInfo.banner},
      premium() {return ['premium', 'admin'].includes(this.$store.state.userInfo.access)},
      showBanner() {return this.$store.state.userInfo.upgrade.upgradePath === 3 && !this.premium},
      canDismiss() {return this.$store.state.userInfo.upgrade.canDismiss}
    },
    methods: {
      initScrollbar() {
        let isWindows = navigator.platform.startsWith('Win');
        if (isWindows) {
          initScrollbar('scrollbar-inner');
        }
      }
    },
    mounted() {
      this.initScrollbar();
      store.dispatch('getUserInfo')
        .catch(() => window.location.href = '/login');
    }
  };
</script>
