<script>
  import {mapState } from 'vuex'
  import {cleanPinyin, cleanDefinitions, cleanDecomposition, checkDecomposition} from '@/util/dictionary'

  import CollapseTableCard from './collapse-table-card'
  import DictionaryPlayButton from './dictionary-play-button'
  import VueLoading from 'vue-loading-overlay';
  import CharacterAnimation from "../../components/Lesson/CharacterAnimation";
  import DictionaryAddButton from "./dictionary-add-button";
  import DynamicTitle from "../../components/DynamicTitle";

  export default {
    name: "DictionaryDetails",
    title () {
      return 'Definition | ChinesePod'
    },
    computed: {
      ...mapState({
        charSet: state => state.userInfo.charSet
      })
    },
    components: {
      DynamicTitle,
      DictionaryAddButton,
      CharacterAnimation,
      DictionaryPlayButton,
      CollapseTableCard,
      VueLoading
    },
    data() {
      return {
        searchTerm: '',
        writer: null,
        showAnimation: false,
        loading: false,
        dictionaryData: {}
      };
    },
    async mounted() {
      const word = this.$route.params.word;
      await this.getDictionaryData(word);
    },
    async beforeRouteUpdate (to, from, next) {
      const word = to.params.word;
      await this.getDictionaryData(word);
      next();
    },
    methods: {
      runSearch () {
        if (this.searchTerm) {
          this.$router.push({path: '/dictionary', query: {search: this.searchTerm}})
        } else {

          this.$router.push({path: '/dictionary'})
        }
      },
      calculateSize(size) {
        if (size > 6) {
          return (100 / (size / 6))
        }
        return 100
      },
      cleanPinyin: cleanPinyin,
      cleanDefinitions: cleanDefinitions,
      cleanDecomposition: cleanDecomposition,
      checkDecomposition: checkDecomposition,
      async getDictionaryData(word) {
        this.loading = true;
        const response = await this.axios.get(process.env.VUE_APP_ROOT_API + '/api/v1/dictionary/get-details?word=' + word)
          .catch(() => {});

        if (!response) {
          window.location.href = '/dictionary';
          return
        }

        const data = response.data

        if (!data) {
          window.location.href = '/dictionary';
          return
        }

        try {
          const compoundsList = data.compounds.map(compound => compound)
          const relatedWordsList = data.related.map(related => related)
          const idiomsList = data.idioms.map(idiom => idiom)
          const sampleSentenceList = data.lessons.map(lesson => lesson)
          const definitionList = data.definition.map(definition => {
            const items = definition.definition.split('/')
            return {
              ...definition,
              items
            }
          })

          let decompositionList = []
          for (let [key, value] of Object.entries(data.decomposition)) {
            decompositionList.push(value)
          }
          const animationCharacters = Array.from(word);

          this.loading = false;

          // throw new Error('Something went wrong')

          this.dictionaryData = {
            simplified: data.definition[0].simplified,
            traditional: data.definition[0].traditional,
            pinyin: data.definition[0].pinyin,
            audioUrl: data.audioUrl,
            definitionList,
            decompositionList,
            compoundsList,
            relatedWordsList,
            idiomsList,
            sampleSentenceList,
            animationCharacters
          }

        } catch (e) {
          return await this.$router.push('/dictionary')
        }

      },
      getButtonType(type) {
        return type.toLowerCase().split(' ').join('-')
        if (type === 'Newbie') return 'primary'
        if (type === 'Intermediate') return 'warning'
        if (type === 'Upper Intermediate') return 'danger'
        if (type === 'Elementary') return 'success'
        if (type === 'Media') return 'default'
        return 'primary'
      },
      playAnimation(index, isStartAnimation = false) {
        this.writer.setCharacter(this.animationCharacters[index])

        if (index === 0 && !isStartAnimation) return

        this.writer.animateCharacter({
          onComplete: () => {
            setTimeout(() => {
              this.playAnimation((index + 1) % this.animationCharacters.length)
            }, 1000)
          }
        })
      }
    }
  };
</script>

