<template>
  <card>
    <div slot="header" class="row align-items-center">
      <div class="col-8">
        <h3 class="mb-0">Edit settings </h3>
      </div>
<!--      <div class="col-4 text-right">-->
<!--        <base-button type="success" class="btn btn-sm" @click.prevent="updateSettings()" :loading="loadingProfile" :disabled="loading || loadingProfile">Update</base-button>-->
<!--      </div>-->
    </div>
    <div v-if="loading" class="col-12 text-center text-lg">
      <i class="fas fa-spinner fa-spin"></i>
    </div>
<!--    <validation-observer v-slot="{handleSubmit}" ref="formValidator">-->
      <div v-if="!loading">
<!--            @submit.prevent="handleSubmit(updateSettings)">-->
        <h6 class="heading-small text-muted mb-4">User information</h6>
        <div class="pl-lg-4">
          <div class="row">
            <div class="col-lg-6">
              <base-input name="Chinese Level" label="Current Chinese Level">
                <span class="h2 font-weight-bol">
                {{levelToTitle(userSettings.level)}}
                </span>
                <base-dropdown>
                  <base-button slot="title-container" type="primary" class="dropdown-toggle ml-3"  outline :loading="changingLevel"><i :class="[false ? 'fas fa-spinner fa-spin' : 'ni ni-settings-gear-65']"></i> Change</base-button>
                  <li>
                    <a class="dropdown-item" @click="changeLevel('newbie')">
                      <i class="ni ni-hat-3 newbie"></i> Newbie
                    </a>
                  </li>
                  <li>
                    <a class="dropdown-item" @click="changeLevel('elementary')">
                      <i class="ni ni-hat-3 elementary"></i> Elementary
                    </a>
                  </li>
                  <li>
                    <a class="dropdown-item" @click="changeLevel('preInt')">
                      <i class="ni ni-hat-3 preint"></i> Pre Intermediate
                    </a>
                  </li>
                  <li>
                    <a class="dropdown-item" @click="changeLevel('intermediate')">
                      <i class="ni ni-hat-3 intermediate"></i> Intermediate
                    </a>
                  </li>
                  <li>
                    <a class="dropdown-item" @click="changeLevel('upperInt')">
                      <i class="ni ni-hat-3 upperint"></i> Upper Intermediate
                    </a>
                  </li>
                  <li>
                    <a class="dropdown-item" @click="changeLevel('advanced')">
                      <i class="ni ni-hat-3 advanced"></i> Advanced
                    </a>
                  </li>
                </base-dropdown>
              </base-input>
            </div>
            <div class="col-lg-6">
              <base-input
                label="Chosen Character Set"
                name="Character Set">
              <button-radio-group
                :options="charSetOptions"
                button-classes="btn-secondary"
                @change="changeCharSet(userSettings.charSet)"
                v-model="userSettings.charSet">
              </button-radio-group>
              </base-input>
            </div>
          </div>
          <div class="row">
            <div class="col-lg-6">
              <base-input name="Automatically Mark Studied" label="Automatically Mark Finished Lessons as Studied">
                <base-checkbox v-model="userSettings.autoMarkStudied" @input="toggleAutoMarkStudied($event)">
                  Automatically mark lessons as finished when finishing video or audio materials
                </base-checkbox>
              </base-input>
            </div>
            <div class="col-lg-6">
              <base-input name="pinyin" label="Show Pinyin">
                <base-checkbox v-model="userSettings.pinyin" @input="togglePinyin($event)">
                  Show pinyin alongside Chinese Characters
                </base-checkbox>
              </base-input>
            </div>
          </div>
        </div>
      </div>
<!--      </form>-->
<!--    </validation-observer>-->
  </card>
</template>
<script>
  import swal from 'sweetalert2';
  import {mapState, mapActions} from 'vuex';
  import ButtonRadioGroup from "../../../components/ButtonRadioGroup";

  export default {
    components: {ButtonRadioGroup},
    data() {
      return {
        validated: false,
        userSettings: {},
        loadingProfile: false,
        changingLevel: false,
        loading: false,
        disabledPassword: true,
        charSetOptions: [{value: 'simplified', label: 'Simplified Chinese'}, {value: 'traditional', label: 'Traditional Chinese'}]
      };
    },
    computed: {
      ...mapState({
        userInfo: 'userInfo'
      })
    },
    async beforeMount() {
      await this.loadProfile();
    },
    methods: {
      async loadProfile () {
        this.loading = true;
        await this.$store.dispatch('getUserInfo');
        this.userSettings = (({pinyin, autoMarkStudied, charSet, level}) => ({pinyin, autoMarkStudied, charSet, level}))(this.userInfo);
        this.loading = false;
      },
      levelToTitle (string) {
        const map = {
          newbie: 'Newbie',
          elementary: 'Elementary',
          preint: 'Pre Intermediate',
          intermediate: 'Intermediate',
          upperint: 'Upper Intermediate',
          advanced: 'Advanced',
          media: 'Media'
        };
        return map[string.toLowerCase()]
      },
      async changeLevel (level) {
        this.changingLevel = true;
        await this.$store.dispatch('changeLevel', level);
        this.userSettings.level = level;
        this.changingLevel = false;
      },
      ...mapActions({
        changeCharSet: 'changeCharSet',
        togglePinyin: 'togglePinyin',
        toggleAutoMarkStudied: 'toggleAutoMarkStudied'
      })
    }
  };
</script>
