<template>
  <modal :show.sync="showModal"
         body-classes="p-0"
         modal-classes="modal-dialog-centered modal-md"
         @close="$emit('close')">
    <h6 slot="header" class="modal-title" id="modal-title-notification">Update your credit card</h6>
    <card type="secondary" shadow
          header-classes="bg-white pb-5"
          body-classes="px-lg-5 py-lg-5"
          class="card border-0">
      <template>
        <form role="form">
          <base-input
            type="text"
            label="Name on card"
            name="name on card"
            rules="required"
            placeholder="Name on card"
            success-message="Looks good!"
            addon-left-icon="ni ni-single-02"
            v-model="fullName"
          >
          </base-input>
          <div class="form-group">
            <div class="input-group-alternative">
              <stripe-card class='form-control stripe-card'
                           :class='{ complete }'
                           :stripe='stripe'
                           :options='options'
                           @change='complete = $event.complete'
              />
            </div>
          </div>
          <div class="text-center">
            <base-button type="success" class="my-2" :loading="loading" :disabled="!complete" @click.prevent="updateCard" @keyup.enter="updateCard">Update</base-button>
          </div>
        </form>
      </template>
    </card>
  </modal>
</template>

<script>
  import { Card, createToken } from 'vue-stripe-elements-plus'

  export default {
    name: "UpdateCard",
    components: { 'stripe-card': Card },
    props: {
      showModal: Boolean,
      subscriptionId: String
    },
    data () {
      return {
        fullName: '',
        loading: false,
        complete: false,
        options: {}
      }
    },
    computed: {
      stripe () {
        return process.env.VUE_APP_STRIPE_KEY
      }
    },
    methods: {
      async updateCard () {
        this.loading = true;
        let stripeData = await createToken({name: this.fullName});
        if (stripeData && stripeData.token && stripeData.token.id) {
          await this.axios.post(process.env.VUE_APP_ROOT_API + `/api/v1/account/subscriptions/update`, {
            fullName: this.fullName,
            token: stripeData.token.id,
            subscriptionId: this.subscriptionId
          })
            .then(() => {
              this.$notify({type: 'success', message: `Your card has been updated`});
              this.$emit('updated')
            })
            .catch((err) => {
              this.$notify({
                type: 'danger',
                verticalAlign: 'top',
                horizontalAlign: 'right',
                title: 'Update Error',
                icon: 'fas fa-exclamation',
                message: `Sorry, something went wrong. If this happens again, please reach out to support@chinesepod.com`,
                timeout: 10000,
                showClose: true
              });
            })
        }
        this.loading = false
      }
    }
  }
</script>

