<script>
  export default {
    name: "LessonRating",
    beforeRouteEnter(to, from, next) {
      // check before enter the route
      // if check failed, you can cancel this routing

      const checkParams = () => {
        let params = to.params
        return params.slug && params.rating && Number.isInteger(parseInt(params.rating))
      }

      // fake code here
      const isCheckPassed = checkParams();

      if (isCheckPassed) {
        next();
      } else {
        // cancel this routing
        // or redirect to the page you want with next function
        next({
          name: 'lesson',
          params: to.params
        });
      }
    },
    async mounted () {
      await this.$store.dispatch('leaveLessonRating', this.$route.params)
    },
    methods: {

    }
  }
</script>

