<template>
  <div>
    <base-header class="pb-md-6" type="">
      <div class="row align-items-center py-4">
        <div class="col-9 col-lg-10">
          <router-link to="/home" class="d-inline-block d-md-none mr-2"><i class="fas fa-home"></i> - Home</router-link>
          <h1 class="h2 d-inline-block mb-0 mr-md-4">Course List</h1>
          <nav aria-label="breadcrumb" class="d-none d-md-inline-block">
            <ol class="breadcrumb breadcrumb-links">
              <li class="breadcrumb-item"><router-link to="/"><i class="fas fa-home"></i></router-link></li>
              <li class="breadcrumb-item"><router-link to="/home">Home</router-link></li>
              <li class="breadcrumb-item active" aria-current="page">Courses</li>
            </ol>
          </nav>
        </div>
      </div>
    </base-header>
    <!--    COURSE CONTENT-->
    <div class="container-fluid mt-md--6">
      <div v-if="courseListData.length < 1" class="row">
        <div  class="col-12">
          <div class="row">
            <div class="col-sm-8 offset-sm-2">
              <div class="col-12 d-flex align-items-center pt-4 pt-sm-0 lesson-loading">
                <i class="fas fa-spinner fa-spin mx-auto"></i>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div v-if="courseListData.length > 0">
        <div v-if="isMobile()">
          <collapse :multiple-active="false">
            <div v-for="(level, index) in levels" :key="index">
              <div class="col-12 pb-4">
                <div class="row">
                  <div class="h2 display-1">{{ level.title }}</div>
                  <badge size="lg" class="my-auto ml-2" :class="level.title.toLowerCase()">Level</badge>
                </div>
                <p class="h3">{{ level.description }}</p>
              </div>
              <collapse-item :id="level.title">
                <h5 slot="title" class="mb-0" :class="`text-${level.title.toLowerCase()}`">
                  <span class="p-2 folder-icon mr-2" :class="`icon-${level.title.toLowerCase()}`">
                    <i class="far fa-folder-open"></i>
                  </span>
                  View Courses
                </h5>
                <div class="row">
                  <div v-for="course in courseListData.slice(index * 6, (index + 1) * 6)" :key="course.id" class="col-6 col-md-2">
                    <course-card  :course="course" />
                  </div>
                </div>
              </collapse-item>
            </div>
          </collapse>
        </div>
        <div v-else>
          <div v-for="(level, index) in levels" :key="index">
            <div class="col-12 pb-4">
              <div class="row">
                <div class="h2 display-1">{{ level.title }}</div>
                <badge size="lg" class="my-auto ml-2" :class="level.title.toLowerCase()">Level</badge>
              </div>
              <p class="h3">{{ level.description }}</p>
            </div>
            <div class="row">
              <div v-for="course in courseListData.slice(index * 6, (index + 1) * 6)" :key="course.id" class="col-6 col-md-2">
                <course-card  :course="course" />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import preloaderSquare from '@/assets/img/preloader-square.svg'
  import {mapState} from 'vuex'
  import CourseCard from "../../components/Dashboard/CourseCard";
  import CollapseItem from "../../components/Collapse/CollapseItem";
  import Collapse from "../../components/Collapse/Collapse";

  export default {
    name: "CourseList",
    components: {Collapse, CollapseItem, CourseCard},
    title: () => `Lesson Courses | ChinesePod`,
    data () {
      return {
        courseListData: [],
        preloaderSquare: preloaderSquare,
        enrolled: false,
        levels: [
          {
            title: 'Newbie',
            imgUrl: 'https://www.chinesepod.com/images/levels/newbie/cpod_level_newbie.svg',
            description: 'If youre just trying to learn some survival Chinese or looking to start a lifelong ' +
              'relationship with the language, heres where to start.',
            moreLessons: { path: 'explore', query: { search: '', newbie: true } }
          },
          {
            title: 'Elementary',
            imgUrl: 'https://www.chinesepod.com/images/levels/elementary/cpod_level_elementary.svg',
            description: 'When you can get by with some key phrases, but are looking to start having basic' +
              ' conversations in Chinese, elementary lessons will help you make the jump. Builds vocabulary base and' +
              ' introduces simple grammar structures.Lessons mostly spoken in English with approximately' +
              ' 30% Mandarin.',
            moreLessons: { path: 'explore', query: { search: '', elementary: true } }
          },
          {
            title: 'Pre-Intermediate',
            imgUrl: 'https://www.chinesepod.com/images/levels/pre-intermediate/cpod_level_pre-intermediate.svg',
            description: 'Explore broader contexts and applications of frequently used expressions needed to ' +
              'navigate comfortably in a Mandarin speaking environment. Areas of relevance includes small talk ' +
              'with new acquaintances about daily life; job-seeking and workspace conversations; shopping; and ' +
              'local geography. The lessons will definitely prepare you for living in a Chinese speaking community.',
            moreLessons: { path: 'explore', query: { search: '', 'pre intermediate': true } }
          },
          {
            title: 'Intermediate',
            imgUrl: 'https://www.chinesepod.com/images/levels/intermediate/cpod_level_intermediate.svg',
            description: 'Lessons are given in half Mandarin and half English to help you level up quickly. ' +
              'These lessons cover conversational and situational topics, as well as drama series, that naturally ' +
              'engage adult learners in their mother tongue. Many intermediate lessons are relevant to current ' +
              'affairs, so lessons can deepen your knowledge about one certain topic while learning and increase ' +
              'your confidence level while speaking Chinese. The best way to advance and master this level is to ' +
              'choose and learn with what interests you.',
            moreLessons: { path: 'explore', query: { search: '', intermediate: true } }
          },
          {
            title: 'Upper-Intermediate',
            imgUrl: 'https://www.chinesepod.com/images/levels/upper-intermediate/cpod_level_upper-intermediate.svg',
            description: 'Upper-intermediate lessons are for students with conversational fluency but looking to use' +
              ' more sophisticated language and tackle more specialized topics. Advanced grammar patterns and ' +
              'specialized vocabulary. Primarily in Chinese, with some English.',
            moreLessons: { path: 'explore', query: { search: '', 'upper intermediate': true } }
          },
          {
            title: 'Advanced',
            imgUrl: 'https://www.chinesepod.com/images/levels/advanced/cpod_level_advanced.svg',
            description: 'Advanced lessons are the black-belt of ChinesePod: you can already speak circles around ' +
              'your friends, but are looking for complete fluency Chinese.',
            moreLessons: { path: 'explore', query: { search: '', advanced: true } }
          }
        ],
        courseTitles: [
          {id: 0, title: 'Newbie Courses'},
          {id: 1, title: 'Elementary Courses'},
          {id: 2, title: 'Pre-Intermediate Courses'},
          {id: 3, title: 'Intermediate Courses'},
          {id: 4, title: 'Upper-Intermediate Courses'},
          {id: 5, title: 'Advanced Courses'}
        ]
      }
    },
    computed: {
      ...mapState({
        courses: 'courses'
      })
    },
    methods: {
      async getCourses () {
        this.courseListData = await this.axios.get(process.env.VUE_APP_ROOT_API + `/api/v1/dashboard/all-courses`)
          .then(({data}) => {
            data.forEach((course) => {
              course.enrolled = this.courses.filter((item) => item.course.id === course.id).length > 0
            });
            return data
          })
          .catch(() => {window.location.href = '/home'});
      },
      isMobile() {
        return /Android|webOS|iPhone|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);
      }
    },
    async beforeMount () {
      await this.getCourses();
    }
  }
</script>

