<template>
  <card>
    <div slot="header" class="row align-items-center">
      <div class="col-8">
        <h3 class="mb-0">Edit profile </h3>
      </div>
      <div class="col-4 text-right">
        <base-button type="success" class="btn btn-sm" @click.prevent="updateProfile()" :loading="loadingProfile" :disabled="loading || loadingProfile">Update</base-button>
      </div>
    </div>
    <div class="col-12 text-center text-lg">
      <i v-if="loading" class="fas fa-spinner fa-spin"></i>
    </div>
    <validation-observer v-slot="{handleSubmit}" ref="formValidator">
      <form v-if="!loading" class="needs-validation"
            @submit.prevent="handleSubmit(updateProfile)">
        <h6 class="heading-small text-muted mb-4">User information</h6>

        <div class="pl-lg-4">
          <div class="row">
            <div class="col-lg-6">
              <base-input
                type="email"
                label="Email address"
                name="email address"
                placeholder="podster@email.com"
                success-message="Looks good!"
                :rules="{required: true, email: true}"
                @keyup.enter="handleSubmit(updateProfile)"
                v-model="userProfile.email"
              >
              </base-input>
            </div>
            <div class="col-lg-6" @click="disabledPassword = false">
              <base-input
                prepend-icon="ni ni-lock-circle-open"
                label="Password"
                placeholder="Password"
                name="password"
                type="password"
                rules="min:6"
                success-message="Looks good!"
                @keyup.enter="handleSubmit(updateProfile)"
                autocomplete="none"
                :disabled="disabledPassword"
                v-model="userProfile.password">
              </base-input>
            </div>
          </div>
          <div class="row">
            <div class="col-lg-6">
              <base-input
                type="text"
                label="Username"
                name="username"
                helperText="Your username will appear on comments you leave"
                :rules="{required: true, min: 6}"
                placeholder="Username"
                success-message="Looks good!"
                @keyup.enter="handleSubmit(updateProfile)"
                v-model="userProfile.username"
              >
              </base-input>
            </div>
            <div class="col-lg-6">
              <base-input
                type="text"
                label="Full Name"
                name="full name"
                helperText="Your name will remain private and will only be used to identify your account"
                rules="required"
                placeholder="Full Name"
                success-message="Looks good!"
                @keyup.enter="handleSubmit(updateProfile)"
                v-model="userProfile.name"
              >
              </base-input>
            </div>
          </div>
        </div>

      </form>
    </validation-observer>
  </card>
</template>
<script>
  import swal from 'sweetalert2';
  import {mapState} from 'vuex';

  export default {
    data() {
      return {
        validated: false,
        userProfile: {},
        loadingProfile: false,
        loading: false,
        disabledPassword: true
      };
    },
    computed: {
      ...mapState({
        userInfo: 'userInfo'
      })
    },
    async beforeMount() {
      await this.loadProfile();
    },
    methods: {
      async loadProfile () {
        this.loading = true;
        await this.$store.dispatch('getUserInfo');
        this.userProfile = (({email, name, username}) => ({email, name, username}))(this.userInfo);
        this.loading = false;
      },
      async updateProfile() {
        this.loadingProfile = true;
        let profile = this.userProfile;
        let data = {};
        Object.keys(profile).forEach(key => {
          if (profile[key]) {
            data[key] = profile[key]
          }
        });
        await this.axios.put(process.env.VUE_APP_ROOT_API + '/api/v1/account/profile/update', data)
          .catch((err) => swal.fire({
            icon: 'error',
            title: 'Oops...',
            text: 'Your email and username have to be unique!',
            footer: '<a href="mailto:support@chinesepod.com">Why do I have this issue?</a>'
          }));
        await this.loadProfile();
        this.loadingProfile = false;
      }
    }
  };
</script>
<style></style>
