import swal from "sweetalert2";
<template>
  <card>
    <div slot="header" class="row align-items-center">
      <div class="col-8">
        <h3 class="mb-0">Subscription</h3>
      </div>
    </div>
    <div class="">
      <div class="row">
        <div v-if="loading" class="col-12 text-center text-lg">
          <div class="col-12">
            <i class="fas fa-spinner fa-spin"></i>
          </div>
        </div>
        <div v-if="!loading" class="col-12 p-0 m-0">
          <div v-if="subscriptionData && subscriptionData.length > 0" class="col-12">
            <div v-for="subscription in subscriptionData" :key="subscription.id">
              <div class="card bg-gradient-lighter col-lg-8 offset-lg-2">
                <!-- Card body -->
                <div class="card-body">
                  <div class="row justify-content-between align-items-center">
                    <div class="col">
                      <div class="h4">
                        {{ subscription.product_id.product_title }}
                      </div>
                    </div>
                    <div class="col-auto">
                      <span class="badge badge-lg" :class="`badge-${subscription.status === 1 ? 'success' : 'danger'}`">{{subscription.status === 1 ? 'Active' : 'Renewal Stopped'}}</span>
                    </div>
                  </div>
                  <div v-if="subscription.cc_num" class="my-2">
                <span class="h6 surtitle">
                  Card number
                </span>
                    <div class="card-serial-number h3">
                      <div class="h3" v-for="(part, index) in ['####', '####', '####', subscription.cc_num]" :key="index">
                        {{part}}
                      </div>
                    </div>
                  </div>
                  <div class="row">
                    <div class="col-12 col-md-6">
                      <span class="h6 surtitle ">{{ subscription.status === 1 ? 'Next billing date' : 'Subscription expiry date' }}</span>
                      <span class="d-block h3">{{ formatDate(subscription.next_billing_time) }}</span>
                    </div>
<!--                    <div v-if="subscription.status !== 1"  class="col-12 col-md-6">-->
<!--                      <span class="h6 surtitle ">Stopped on</span>-->
<!--                      <span class="d-block h3">{{ formatDate(subscription.date_cancelled) }}</span>-->
<!--                    </div>-->
                  </div>
                  <div class="row">
                    <div v-if="subscription.status === 1" class="col-12">
                      <div class="row justify-content-end">
                        <a v-if="subscription.subscription_type !== 2" href="/upgrade" class="col-12 col-md-4 pb-2">
                          <base-button block size="sm" type="success">Upgrade</base-button>
                        </a>
                        <div v-if="subscription.subscription_from === 7" class="col-12 col-md-4 pb-2">
                          <base-button block size="sm" type="warning" @click="updateSubscriptionCard(subscription)">Update Card</base-button>
                        </div>
                        <div class="col-12 col-md-4 pb-2">
                          <base-button block size="sm" type="danger" @click="cancelSubscription(subscription)">Stop Renewal</base-button>
                        </div>
                      </div>
                    </div>
                    <div v-if="subscription.status === 2 && subscription.subscription_from === 7" class="col-12">
                      <div class="row justify-content-end">
                        <div class="col-12 col-md-4 pb-2">
                          <base-button block size="sm" type="success" @click="restartSubscription(subscription)">Restart</base-button>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <update-card v-if="modal" :show-modal="modal" :subscription-id="subscriptionId" @updated="refreshSubscription()" @close="modal = false"/>
          <div v-if="showUpgrade" class="col-12">
            <upgrade hide-bullets />
          </div>
        </div>
      </div>
    </div>
  </card>
</template>
<script>

  import Upgrade from "../../../components/Lesson/Upgrade";
  import swal from 'sweetalert2';
  import bugsnagClient from "../../../lib/bugsnag";
  import UpdateCard from "./UpdateCard";

  export default {
    components: {UpdateCard, Upgrade},
    props: {
      subscriptionData: {
        type: Array
      },
      loading: {
        type: Boolean,
        default: true
      }
    },
    computed: {
      showUpgrade () {
        return !this.premium // || (this.subscriptionData && !this.subscriptionData.filter(subscription => subscription.status === 1).length)
      },
      access() {
        return this.$store.state.userInfo ? this.$store.state.userInfo.access : 'free'
      },
      premium () {
        return ['premium', 'admin'].includes(this.access)
      }
    },
    data() {
      return {
        modal: false,
        subscriptionId: '',
        userSettings: {}
      };
    },
    created() {
      let script = document.createElement('script');
      script.async = true;
      script.src = "https://js.stripe.com/v3/";
      document.head.appendChild(script);
    },
    methods: {
      async refreshSubscription () {
        this.modal = false;
        this.$emit('updated')
      },
      formatDate(date) {
        date = new Date(date);
        if (date.getFullYear() === new Date().getFullYear()) {
          return date.toLocaleString('en-US', {day: '2-digit', month: 'long'});
        } else {
          return date.toLocaleString('en-US', {year: 'numeric', day: '2-digit', month: 'long'});
        }
      },
      async cancelSubscription (subscription) {
        if (subscription.subscription_from === 7) {
          swal.queue([{
            title: 'Are you sure?',
            text: "Once renewal is cancelled, you will still be able to use ChinesePod until your current subscription period expires!",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#f5365c',
            cancelButtonColor: '#adb5bd',
            confirmButtonText: 'Yes, cancel!',
            cancelButtonText: 'Back',
            showLoaderOnConfirm: true,
            preConfirm: () => {
              return this.$http.post(process.env.VUE_APP_ROOT_API + `/api/v1/account/subscriptions/cancel`, {
                subscriptionId: subscription.subscription_id
              })
                .then(() => {
                  this.$emit('updated');
                  swal.insertQueueStep({
                    icon: 'success',
                    title: 'Your subscription\'s renewal has been cancelled!'
                  })
                })
                .catch((e) => {
                  bugsnagClient.notify(e);
                  swal.insertQueueStep({
                    icon: 'error',
                    title: 'Sorry, we were unable to process your cancellation at this time. If you encounter this issue again, please contact us at support@chinesepod.com and we\'ll process your cancellation manually'
                  })
                })
            }
          }]);
        } else if (subscription.subscription_from === 5) {
          swal.fire({
            title: 'Are you sure?',
            text: "Once cancelled, you will still be able to use ChinesePod until your current subscription period expires! However, we're unable to process iTunes subscriptions directly. Please use your phone to cancel this subscription.",
            icon: 'warning',
            confirmButtonColor: '#adb5bd',
            confirmButtonText: 'Back'
          })
        } else if (subscription.subscription_from === 7) {
          swal.fire({
            title: 'Are you sure?',
            text: "Once cancelled, you will still be able to use ChinesePod until your current subscription period expires! However, we're unable to process Google Play subscriptions directly. Please use your phone to cancel this subscription.",
            icon: 'warning',
            confirmButtonColor: '#adb5bd',
            confirmButtonText: 'Back'
          })
        } else {
          swal.fire({
            title: 'Are you sure?',
            text: "Once cancelled, you will still be able to use ChinesePod until your current subscription period expires! Unfortunately, your subscriptions is an older version and you'll need to process it on the old dashboard.",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#f5365c',
            cancelButtonColor: '#adb5bd',
            confirmButtonText: 'Cancel',
            cancelButtonText: 'Back'
          }).then((result) => {
            if (result.value) {
              window.location.href = 'https://chinesepod.com/accounts/payments/'
            }
          })
        }
      },
      async restartSubscription (subscription) {
        if (subscription.subscription_from === 7) {
          swal.queue([{
            title: 'Restart your subscription',
            text: "We will restart your subscription today, buy you will only be charged at your next renewal date!",
            icon: 'info',
            showCancelButton: true,
            confirmButtonColor: '#2dce89',
            cancelButtonColor: '#f5365c',
            confirmButtonText: 'Yes, restart!',
            cancelButtonText: 'Back',
            showLoaderOnConfirm: true,
            preConfirm: () => {
              return this.$http.post(process.env.VUE_APP_ROOT_API + `/api/v1/account/subscriptions/restart`, {
                subscriptionId: subscription.subscription_id
              })
                .then(() => {
                  this.$emit('updated');
                  swal.insertQueueStep({
                    icon: 'success',
                    title: 'Your subscription has been restarted! Enjoy your studies.'
                  });
                })
                .catch((e) => {
                  bugsnagClient.notify(e);
                  swal.insertQueueStep({
                    icon: 'error',
                    title: 'Sorry, we were unable to process your request at this time. If you encouter this issue again, please contact us at support@chinesepod.com and we\'ll process your renewal manually'
                  })
                })
            }
          }]);
        } else if (subscription.subscription_from === 5) {
          swal.fire({
            title: 'Are you sure?',
            text: "Once cancelled, you will still be able to use ChinesePod until your current subscription period expires! However, we're unable to process iTunes subscriptions directly. Please use your phone to cancel this subscription.",
            icon: 'warning',
            confirmButtonColor: '#adb5bd',
            confirmButtonText: 'Back'
          })
        } else if (subscription.subscription_from === 7) {
          swal.fire({
            title: 'Are you sure?',
            text: "Once cancelled, you will still be able to use ChinesePod until your current subscription period expires! However, we're unable to process Google Play subscriptions directly. Please use your phone to cancel this subscription.",
            icon: 'warning',
            confirmButtonColor: '#adb5bd',
            confirmButtonText: 'Back'
          })
        } else {
          swal.fire({
            title: 'Are you sure?',
            text: "Once cancelled, you will still be able to use ChinesePod until your current subscription period expires! Unfortunately, your subscriptions is an older version and you'll need to process it on the old dashboard.",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#f5365c',
            cancelButtonColor: '#adb5bd',
            confirmButtonText: 'Cancel',
            cancelButtonText: 'Back'
          }).then((result) => {
            if (result.value) {
              window.location.href = 'https://chinesepod.com/accounts/payments/'
            }
          })
        }
      },
      updateSubscriptionCard (subscription) {
        this.subscriptionId = subscription.subscription_id;
        this.modal = true
      }
    }
  };
</script>
<style></style>
