<template>
  <base-button
    @click.native="handleDeleteOne(row)"
    class="remove btn-link btn-icon-only text-white "
    type="danger"
    size="sm"
    icon
    :loading="loading"
  >
    <i v-show="!loading" class="text-white fa fa-trash"></i>
  </base-button>
</template>

<script>
  export default {
    name: "DeleteButton",
    props: {
      row: Object,
      character: String
    },
    data () {
      return {
        loading: false
      }
    },
    methods: {
      async handleDelete(row) {
        await this.axios.delete(process.env.VUE_APP_ROOT_API + '/api/v1/vocabulary/words/' + row.user_vocabulary_id)
          .then(() => this.$notify({type: 'warning', message: `Deleted ${this.character} from your vocabulary`}))
          .catch(() => this.$notify({type: 'danger', message: `Failed to delete ${this.character} from your vocabulary. If this happens again, please reach out to support@chinesepod.com`}))
      },
      async handleDeleteOne(row) {
        this.loading = true;
        await this.handleDelete(row);
        this.$emit('deleted');
        this.loading = false
      }
    }
  }
</script>

