<template>
  <base-button outline class="text-center text-capitalize btn-play" :class="[square ? 'd-sm-inline-block' : 'd-none d-sm-inline-block']" @click="togglePlay">
    <i class="fa mr-0 play" :class="playing ? 'fa-pause' : 'fa-play'"/>
    {{ playing ? 'Pause' : 'Play' }}
  </base-button>
</template>

<script>


  export default {
    name: 'dictionary-play-button',
    props: {
      audioUrl: String,
      square: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        playing: false,
        audio: ''
      }
    },
    mounted() {
      this.audio = new Audio(this.audioUrl);
      this.audio.addEventListener('ended', () => this.playing = false);
    },
    methods: {
      async togglePlay () {
        if(!this.playing) {
          await this.audio.play();
          this.playing = true;
        } else {
          await this.audio.pause();
          this.playing = false;
        }

      }
    }
  }
</script>

