<template>
  <div>
    <base-header class="pb-md-6" type="">
      <div class="row align-items-center py-4">
        <div class="col-9 col-lg-10">
          <router-link to="/home" class="d-inline-block d-md-none mr-2"><i class="fas fa-home"></i> - Home</router-link>
          <h1 class="h2 d-inline-block mb-0 mr-md-4">Search Dictionary</h1>
          <nav aria-label="breadcrumb" class="d-none d-md-inline-block">
            <ol class="breadcrumb breadcrumb-links">
              <li class="breadcrumb-item"><router-link to="/"><i class="fas fa-home"></i></router-link></li>
              <li class="breadcrumb-item active" aria-current="page"><router-link to="/dictionary">Dictionary</router-link></li>
            </ol>
          </nav>
        </div>
      </div>
    </base-header>
    <div class="container-fluid mt-md--6">
      <div id="searchArea" class="col-12 col-lg-8 offset-lg-2 col-xl-6 offset-xl-3">
        <base-input group class="mb-2" prepend-icon="fas fa-search">
          <input type="search" class="form-control" placeholder="Search Dictionary" aria-label="Search Dictionary" aria-describedby="button-addon2" v-model.trim="searchTerm" @search="runSearch()">
          <div class="input-group-append">
            <base-button class="btn btn-outline-primary is-loading" type="button" id="button-addon2" @click="runSearch()" :loading="searching">Search</base-button>
          </div>
        </base-input>

      </div>
      <div id="vocabulary">
        <transition name="fade">
          <div v-if="results && results.length" class="pb-2 col-12 col-lg-8 offset-lg-2 col-xl-6 offset-xl-3">
            <router-link v-for="result in results" :key="result.simplified + result.traditional + result.pinyin" :to="`/dictionary/${result[charSet]}`" class="list-group-item list-group-item-action">
              <search-item :phrase="result" />
            </router-link>
          </div>
          <div v-else-if="!searching" class="pb-2 col-12 col-lg-8 offset-lg-2 col-xl-6 offset-xl-3">
            <div class="h3">
              Sorry, we couldn't find anything!
            </div>
            <div>
              Please change your search term and try again. You can try to search in English, Chinese or pinyin.
            </div>
          </div>
        </transition>
        <transition name="fade">
          <div v-if="results && results.length"  id="controls" class="text-center">
            <base-pagination
              v-show="showPagination"
              :page-count="pages"
              :per-page="16"
              :value="page"
              align="center"
              @input="listener($event)"
            ></base-pagination>
          </div>
        </transition>
      </div>
      <loading :active.sync="searching"
               :can-cancel="true"
               :is-full-page="fullPage"></loading>
    </div>
  </div>
</template>

<script>
  import {mapState, mapActions} from 'vuex';

  import pickBy from "lodash.pickby";
  import isEqual from "lodash.isequal";

  // Import component
  import Loading from 'vue-loading-overlay';
  // Import stylesheet
  import 'vue-loading-overlay/dist/vue-loading.css';
  import SearchItem from "../../components/Dictionary/SearchItem";

  export default {
    name: "DictionarySearch",
    components: {SearchItem, Loading},
    computed: {
      ...mapState({
        charSet: state => state.userInfo.charSet})
    },
    title () {
      return 'Dictionary | ChinesePod'
    },
    data () {
      return {
        searchTerm: '',
        results: [],
        searching: false,
        enableCourseSearch: true,
        hideButtons: false,
        fullPage: true,
        perPage: 20,
        page: 1,
        pages: 1,
        showLoadMore: false,
        showPagination: false,
        showAdvancedOptions: false
      }
    },
    methods: {
      async runSearch () {

        let newQuery = {query: {...{search: this.searchTerm}}};

        if (this.searchTerm && !isEqual(this.$route.query, newQuery.query)) {

          this.$router.push({...{name: 'Dictionary Search'}, ...newQuery}).catch(err => {})

        } else {

          await this.searchVocabulary();

        }

      },
      async listener (event) {
        if (!this.searching) {

          await this.$router.push({ name: 'Dictionary Search', query: {...this.$route.query, ...{page: event} } }).catch(err => {})

        }
      },
      async searchVocabulary () {
        this.searching = true;

        await this.axios.get(process.env.VUE_APP_ROOT_API + '/api/v1/search/search-dictionary/'
          + this.searchTerm
          + `?skip=${(this.page - 1) * this.perPage}`)
          .then(({data}) => {

            this.pages = Math.ceil(data.count / this.perPage);

            this.showPagination = this.pages > 1;

            this.results = data.results;

          })
          .catch(() => {});
        this.searching = false;

        if(this.searchTerm) {
          this.$gtag.event('dictionary-search', {
            'event_category': 'engagement',
            'event_label': this.searchTerm
          });
          await this.$store.dispatch('logEvent', {
            action: 'dictionary-search',
            'event_category': 'engagement',
            'event_label': this.searchTerm
          });
        }
      }
    },
    async mounted () {

      if (this.$route.query && this.$route.query.search) {

        this.searchTerm = this.$route.query.search;

      }

      if (this.$route.query && this.$route.query.page) {

        this.page = parseInt(this.$route.query.page);
        this.pages = this.page;

      }

      await this.searchVocabulary();

    },
    async beforeRouteUpdate(to, from, next) {

      let queryData = to.query;

      if (queryData && queryData.search) {
        this.searchTerm = queryData.search;
      } else {
        this.searchTerm = '';
      }

      if (queryData && queryData.page) {
        this.page = parseInt(queryData.page);
      } else {
        this.page = 1;
        this.pages = 1;
      }

      await this.searchVocabulary();

      next()
    }
  }
</script>

