import Vue from 'vue';
import Router from 'vue-router';
import DashboardLayout from '@/views/Layout/DashboardLayout.vue';
import Dashboard from '@/views/CPOD/Dashboard.vue';
import Feedback from '@/views/CPOD/Feedback.vue';
import Connect from '@/views/CPOD/Connect.vue';
import Explore from '@/views/Explore/Explore.vue';
import DictionarySearch from '@/views/Dictionary/Search.vue';
import DictionaryDetails from '@/views/Dictionary/Details.vue';
import Latest from '@/views/CPOD/Latest.vue';
import History from '@/views/CPOD/History.vue';
import Bookmarks from '@/views/CPOD/Bookmarks.vue';
import Levels from '@/views/CPOD/Levels.vue';
import LessonDetails from "@/views/Lesson/LessonDetails";
import CourseDetails from "@/views/Courses/CourseDetails";
import CourseList from "@/views/Courses/CourseList";
import PlaylistList from "@/views/Playlists/PlaylistList";
import PlaylistDetails from "@/views/Playlists/PlaylistDetails";
import Podcast from '@/views/Podcasts/Podcast';
import PageLayout from '@/views/Pages/PageLayout.vue';
import UserProfile from '@/views/Pages/UserProfile.vue';
import UserSettings from '@/views/Pages/UserSettings.vue';
import UserBilling from '@/views/Pages/UserBilling.vue';
import VocabularyIndex from "@/views/Vocabulary/VocabularyIndex";
import DecksIndex from "../views/Vocabulary/DecksIndex";
import DecksDetails from "../views/Vocabulary/DecksDetails";
import LessonRating from "../views/Lesson/LessonRating";

Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/podcasts',
      component: PageLayout,
      name: 'Pages',
      meta: {
        navbarType: 'light'
      },
      children: [
        {
          path: '/podcasts',
          name: 'Podcast',
          component: Podcast
        }
      ]
    },
    {
      path: '/',
      redirect: '/home',
      name: 'Home',
      component: DashboardLayout,
      meta: {
        navbarType: 'light'
      },
      children: [
        {
          path: 'home',
          name: 'Dashboard',
          components: { default: Dashboard },
          meta: {
            navbarType: 'light'
          }
        },
        {
          path: 'explore',
          name: 'Explore',
          components: { default: Explore },
          meta: {
            navbarType: 'light'
          }
        },
        {
          path: 'dictionary',
          name: 'Dictionary Search',
          components: { default: DictionarySearch },
          meta: {
            navbarType: 'light'
          }
        },
        {
          path: 'dictionary/:word',
          name: 'Dictionary Details',
          components: { default: DictionaryDetails },
          meta: {
            navbarType: 'light'
          }
        },
        {
          path: 'latest',
          name: 'Latest Lessons',
          components: { default: Latest },
          meta: {
            navbarType: 'default'
          }
        },
        {
          path: 'history',
          name: 'History',
          components: { default: History }
        },
        {
          path: 'bookmarks',
          name: 'Bookmarks',
          components: { default: Bookmarks }
        },
        {
          path: 'levels',
          name: 'Academic Levels',
          components: { default: Levels }
        },
        {
          path: 'feedback',
          name: 'Feedback',
          components: { default: Feedback }
        },
        {
          path: 'connect',
          name: 'Connect',
          components: { default: Connect }
        },
        {
          path: '/lesson/:slug/:courseId?',
          name: 'lesson',
          component: LessonDetails,
          meta: {
            navbarType: 'light'
          }
        },
        {
          path: '/lesson/:slug/:courseId?/rate/:rating',
          name: 'lesson-rating',
          component: LessonRating,
          meta: {
            navbarType: 'light'
          }
        },
        {
          path: '/courses/:courseId',
          name: 'course',
          component: CourseDetails,
          meta: {
            navbarType: 'light'
          }
        },
        {
          path: '/library/courses/:courseTitle/:courseId',
          name: 'legacyCourses',
          component: CourseDetails,
          meta: {
            navbarType: 'light'
          }
        },
        {
          path: '/courses',
          name: 'courses',
          component: CourseList,
          meta: {
            navbarType: 'light'
          }
        },
        {
          path: '/playlists',
          name: 'playlists',
          component: PlaylistList,
          meta: {
            navbarType: 'light'
          }
        },
        {
          path: '/playlists/:courseId',
          name: 'playlist',
          component: PlaylistDetails,
          meta: {
            navbarType: 'light'
          }
        },
        {
          path: '/vocabulary',
          name: 'vocabulary',
          component: VocabularyIndex,
          meta: {
            navbarType: 'light'
          }
        },
        {
          path: '/vocabulary/decks',
          name: 'decks',
          component: DecksIndex,
          meta: {
            navbarType: 'light'
          }
        },
        {
          path: '/vocabulary/decks/:deckId',
          name: 'decks-details',
          component: DecksDetails,
          meta: {
            navbarType: 'light'
          }
        },
        {
          path: '/profile',
          name: 'profile',
          component: UserProfile,
          meta: {
            navbarType: 'dark'
          }
        },
        {
          path: '/settings',
          name: 'settings',
          component: UserSettings,
          meta: {
            navbarType: 'dark'
          }
        },
        {
          path: '/subscription',
          name: 'subscription',
          component: UserBilling,
          meta: {
            navbarType: 'dark'
          }
        },
        {
          path: '/billing',
          name: 'billing',
          component: UserBilling,
          meta: {
            navbarType: 'dark'
          }
        }
        // {
        //   path: '/:lessonId/:slug?/:courseId?',
        //   name: 'lesson-by-id',
        //   component: LessonDetails,
        //   meta: {
        //     navbarType: 'light'
        //   }
        // },
        // {
        //   path: '/:lessonId/:slug/:courseId?/rate/:rating',
        //   name: 'lesson-by-id-rating',
        //   component: LessonRating,
        //   meta: {
        //     navbarType: 'light'
        //   }
        // }
      ]
    },
    {
      path: '*',
      redirect: '/'
    }
  ],
  scrollBehavior: (to, from ,savedPosition) => {
    if (savedPosition) {
      return savedPosition;
    }
    if (to.hash) {
      return { selector: to.hash };
    }
    return { x: 0, y: 0 };
  }
});
