<template>
  <div>
    <h4 v-if="speaker" class="text-uppercase ls-1 py-3 mb-0">{{ speaker }}</h4>
    <card class="card-pricing bg-lighter border-0 mb-4"
          header-classes="bg-transparent"
          footer-classes="bg-transparent">
      <div slot="header" class="row align-items-center">
        <div class="col-10 col-sm-11">
          <p class="characters">
            <character-item v-for="(character, index) in cn"
                            :key="index"
                            :lesson-id="lessonId"
                            :characterIndex="index"
                            :sentenceIndex="sentenceIndex"
                            :character="character"
                            :type="charSet"
                            :tab="`${tab}-char`"
                            popup/>
          </p>
        </div>
        <div class="col-2 col-sm-1 text-right">
            <base-button class="float-right btn-icon-only m-0 mb-1" @click="showPlayer = !showPlayer" type="secondary"><i class="fas" :class="[ showPlayer ? 'fa-times' : 'fa-volume-up' ]"></i></base-button>
            <base-button class="float-right btn-icon-only" @click="addSentence" type="secondary" :loading="loading"><i v-if="!loading" class="fas fa-plus"></i></base-button>
        </div>
        <div class="col-12 pt-1">
          <vue-plyr v-if="showPlayer" ref="plyr" :options="playerOptions">
            <audio>
              <source :src="audioSource" type="audio/mp3" />
            </audio>
          </vue-plyr>
        </div>
      </div>
      <div slot="default" class="row align-items-center">
        <div class="col-10 col-sm-11">
          <p v-show="showPinyin" class="pinyin">
            <character-item v-for="(character, index) in cn"
                            :key="index"
                            :lesson-id="lessonId"
                            :characterIndex="index"
                            :sentenceIndex="sentenceIndex"
                            :character="character"
                            :tab="`${tab}-pinyin`"
                            type="pinyin"/>
          </p>
        </div>
        <div class="col-2 col-sm-1">
          <base-button class="float-right btn-icon-only" @click="showPinyin = !showPinyin" type="secondary"><i class="fas" :class="[ showPinyin ? 'fa-times' : 'fa-eye' ]"></i></base-button>
        </div>
      </div>
      <div slot="footer" class="row align-items-center">
        <div class="col-10 col-sm-11">
          <p v-show="showEng" class="english">{{ en }}</p>
        </div>
        <div class="col-2 col-sm-1">
          <base-button class="float-right btn-icon-only" @click="showEng = !showEng" type="secondary"><i class="fas" :class="[ showEng ? 'fa-times' : 'fa-eye' ]"></i></base-button>
        </div>
      </div>
    </card>
  </div>
</template>

<script>
  import Card from '@/components/Cards/Card'
  import CharacterItem from "./CharacterItem";

  export default {
    name: "DialogueItem",
    components: {
      CharacterItem,
      Card
    },
    props: {
      lessonId: String,
      speaker: {
        type: String
      },
      sentence: {
        type: String
      },
      cn: {
        type: Array
      },
      en: {
        type: String
      },
      sentenceIndex: Number,
      audioSource: String,
      tab: String
    },
    mounted () {
    },
    computed: {
      access() {
        return this.$store.state.userInfo ? this.$store.state.userInfo.access : 'free'
      },
      traditional() {
        return this.$store.state.userInfo.charSet === 'traditional';
      },
      charSet() {
        return this.$store.state.userInfo ? this.$store.state.userInfo.charSet : 'simplified'
      },
      pinyin: state => state.userInfo.pinyin,
      player () {return this.$refs.plyr.player}
    },
    data () {
      return {
        loading: false,
        showEng: false,
        showPinyin: false,
        showPlayer: false,
        playerOptions: {
          controls: ['play-large', 'play', 'progress', 'current-time','settings'],
          settings: ['captions', 'quality', 'speed', 'loop', 'autoplay'],
          loop: { active: false },
          autoplay: true
        }
      }
    },
    methods: {
      cleanSentence(sentence, type) {
        return sentence.map((component, index) => {
          if (typeof component === 'object') {
            if (type === 'p' && index) {
              return ' ' + component[type]
            } else {
              return component[type]
            }
          } else {
            return component
          }
        }).join('')
      },
      async addSentence () {
        this.loading = true;
        await this.axios.put(process.env.VUE_APP_ROOT_API + '/api/v1/vocabulary/words/add-sentence', {
          s: this.cleanSentence(this.cn, 's'),
          t: this.cleanSentence(this.cn, 't'),
          p: this.cleanSentence(this.cn, 'p'),
          en: this.en,
          audio: this.audioSource,
          lessonId: this.lessonId
        })
          .then(() => {
            this.$notify({type: 'success', message: `Added ${this.cleanSentence(this.cn, [this.charSet.slice(0,1)])} to your vocabulary`});
          })
          .catch(() => {
            this.$notify({type: 'danger', message: `Failed to add to your vocabulary`});
          });
        this.loading = false;
      }
    }
  }
</script>

<style scoped>
  .characters {
    font-size: 30px;
    font-family: 'Noto Sans SC', sans-serif;
    color: black;
  }
  .pinyin {
    font-family: 'Noto Sans SC', sans-serif;
  }
  .pinyin, .english {
    font-size: 24px;
    color: black;
  }
</style>
