      <div class="row align-items-center py-4">
        <div class="col-9 col-lg-10">
          <router-link to="/home" class="d-inline-block d-md-none mr-2"><i class="fas fa-home"></i> - Home</router-link>
          <h1 class="h2 d-inline-block mb-0 mr-md-4">Vocabulary Decks</h1>
          <nav aria-label="breadcrumb" class="d-none d-md-inline-block">
            <ol class="breadcrumb breadcrumb-links">
              <li class="breadcrumb-item"><router-link to="/"><i class="fas fa-home"></i></router-link></li>
              <li class="breadcrumb-item"><router-link to="/home">Home</router-link></li>
              <li class="breadcrumb-item"><router-link to="/vocabulary">Vocabulary</router-link></li>
              <li class="breadcrumb-item active" aria-current="page">Decks</li>
            </ol>
          </nav>
        </div>
      </div>
    </base-header>
    <div class="container-fluid mt-md--6">
      <div class="row">
        <div class="col-12">
          <div v-show="loading" class="row">
            <div class="col-sm-8 offset-sm-2">
              <div class="col-12 d-flex align-items-center pt-4 pt-sm-0 lesson-loading">
                <i class="fas fa-spinner fa-spin mx-auto"></i>
              </div>
            </div>
          </div>
          <transition name="fade">
            <div v-if="!loading" class="card-wrapper">
              <div class="card">
                <div class="card-header border-0">
                  <div class="row">
                    <div class="col-6">
                      <h3 class="mb-0">Created decks</h3>
                    </div>
                    <div class="col-6 text-right">
                      <el-tooltip content="Edit" placement="top">
                        <base-button type="success" icon size="sm" @click="modals.createDeck = true" class="mb-1 mr-0 mr-md-1">
                          <span class="btn-inner--icon"><i class="fas fa-plus"></i></span>
                          <span class="btn-inner--text">Create Deck</span>
                        </base-button>
                      </el-tooltip>
                      <el-tooltip content="Delete" placement="top">
                        <base-button type="danger" icon size="sm" @click="handleDeleteAll" :loading="deleteButtonLoading" :disabled="deleteButtonDisabled">
                          <span class="btn-inner--icon"><i class="fas fa-trash"></i></span>
                          <span class="btn-inner--text">Delete Selected</span>
                        </base-button>
                      </el-tooltip>
                    </div>
                  </div>
                </div>
                <div class="card-body p-0">
                  <div class="col-12 p-0">
                    <div class="col-12 d-flex justify-content-center justify-content-sm-between flex-wrap"
                    >
                      <el-select
                        class="select-primary pagination-select d-none d-md-block"
                        v-model="pagination.perPage"
                        placeholder="Per page"
                      >
                        <el-option
                          class="select-primary"
                          v-for="item in pagination.perPageOptions"
                          :key="item"
                          :label="item"
                          :value="item"
                        >
                        </el-option>
                      </el-select>

                      <div>
                        <base-input v-model="searchQuery"
                                    prepend-icon="fas fa-search"
                                    placeholder="Search...">
                        </base-input>
                      </div>
                    </div>
                    <el-table class="table-responsive table-flush p-0"
                              :data="queriedData"
                              row-key="id"
                              header-row-class-name="thead-light"
                              @sort-change="sortChange"
                              @selection-change="onSelectionChange">
                      <template slot="empty">
                        <div class="py-5">
                          Please Create Some Decks
                        </div>
                      </template>
                      <el-table-column type="selection"
                                       align="left"
                                       min-width="50px">
                      </el-table-column>
                      <el-table-column label="Name"
                                       min-width="150px"
                                       prop="tag"
                      >
                        <template v-slot="{row}">
                          <router-link :to="`/vocabulary/decks/${row.id}`">{{ row.tag }}</router-link>
                        </template>
                      </el-table-column>
                      <el-table-column label="Words"
                                       min-width="150px"
                                       prop="count"
                      >
                      </el-table-column>
                      <el-table-column min-width="180px"
                                       align="right"
                                       label="Actions"
                                       v-if ="true || (!isMobile && !isTablet)">
                        <div slot-scope="{$index, row}" class="d-flex">
                          <base-button
                            class="play btn-link btn-icon-only"
                            type="secondary"
                            size="sm"
                            icon
                            @click="$router.push({name: 'decks-details', params: { deckId: row.id}})"
                          >
                            <i class="fa fa-edit"></i>
                          </base-button>
                          <deck-delete-button
                            :id="row.id"
                            :title="row.tag"
                            @deleted="handleDelete(row.id)"/>
                        </div>
                      </el-table-column>
                    </el-table>
                  </div>
                  <div
                    slot="footer"
                    class="col-12 d-flex justify-content-center justify-content-sm-between flex-wrap py-3"
                  >
                    <div class="">
                      <p class="card-category">
                        Showing {{ from + 1 }} to {{ to }} of {{ total }} entries

                        <span v-if="selectedRows.length">
                  &nbsp; &nbsp; {{selectedRows.length}} deck{{ selectedRows.length > 1 ? 's': ''}} selected
                </span>
                      </p>

                    </div>
                    <base-pagination
                      class="pagination-no-border"
                      v-model="pagination.currentPage"
                      :per-page="pagination.perPage"
                      :total="total"
                    >
                    </base-pagination>
                  </div>
                </div>
              </div>
            </div>
          </transition>
        </div>
      </div>
    </div>
    <modal :show.sync="modals.createDeck">
      <h6 slot="header" class="modal-title">Create a deck</h6>
      <base-input alternative
                  class="mb-3"
                  label="Deck name"
                  placeholder="Name"
                  v-model="deckInfo.tag"
                  @keyup.enter="createDeck"
                  addon-left-icon="ni ni-email-83">
      </base-input>
      <template slot="footer">
        <base-button type="success" @click.prevent="createDeck" :loading="loadingCreatingDeck">Create a deck</base-button>
        <base-button type="link" class="ml-auto" @click="modals.editDeck = false">Close
        </base-button>
      </template>
    </modal>
  </div>
</template>

<script>
  import {mapState} from 'vuex'
  import { BasePagination } from '@/components';
  import clientPaginationMixin from '@/views/Tables/PaginatedTables/clientPaginationMixin'
  import { Table, TableColumn, DropdownMenu, DropdownItem, Dropdown, Select, Option } from 'element-ui'
  import DeckDeleteButton from "./DeckDeleteButton";

  export default {
    name: "DecksIndex",
    mixins: [clientPaginationMixin],
    title() {
      return 'Vocabulary Decks | ChinesePod'
    },
    components: {
      DeckDeleteButton,
      BasePagination,
      [Table.name]: Table,
      [TableColumn.name]: TableColumn,
      [Select.name]: Select,
      [Option.name]: Option,
      [Dropdown.name]: Dropdown,
      [DropdownItem.name]: DropdownItem,
      [DropdownMenu.name]: DropdownMenu
    },
    data () {
      return {
        loading: true,
        loadingDelete: {},
        propsToSearch: ['s', 't', 'p', 'en'],
        tableData: [],
        showPlayer: false,
        wantsPrint: false,
        selectedRows: [],
        playerOptions: {
          controls: ['play-large', 'play', 'progress', 'current-time','settings'],
          settings: ['captions', 'quality', 'speed', 'loop', 'autoplay'],
          loop: { active: false }
        },
        modals: {
          createDeck: false
        },
        deckInfo: {},
        deleteButtonLoading: false,
        deleteButtonDisabled: true,
        loadingCreatingDeck: false,
        isMobile: false,
        isTablet: false
      }
    },
    computed: {
      premium () {
        return ['premium', 'admin'].includes(this.access)
      },
      player () { return this.$refs.plyr.player },
      ...mapState({
        charSet: state => state.userInfo.charSet,
        pinyin: state => state.userInfo.pinyin,
        access: state => state.userInfo.access
      })
    },
    created() {
      window.addEventListener('resize', this.handleResize);
      this.handleResize();
    },
    destroyed() {
      window.removeEventListener('resize', this.handleResize);
    },
    mounted () {
      this.getUserDecks();

    },
    methods: {
      async getUserDecks (page=1) {
        this.loading = true;
        this.pagination.currentPage = page;
        await this.axios.get(process.env.VUE_APP_ROOT_API + '/api/v1/vocabulary/decks')
          .then(({data}) => {
            this.tableData = data;
            this.loading = false;
          })
          .catch(() => {});
      },
      handleDelete(id) {
        this.tableData = this.tableData.filter(deck => deck.id !== id)
      },
      async deleteDeck(id) {
        this.loadingDelete[id] = true;
        await this.axios.delete(process.env.VUE_APP_ROOT_API + '/api/v1/vocabulary/decks/' + id )
          .then(() => {this.tableData = this.tableData.filter(deck => deck.id !== id)})
          .catch();
        this.loadingDelete[id] = false;
      },
      async createDeck() {
        this.loadingCreatingDeck = true
        await this.axios.put(process.env.VUE_APP_ROOT_API + '/api/v1/vocabulary/decks', {
          tag: this.deckInfo.tag
        })
          .then(() => this.getUserDecks())
          .catch()
        this.loadingCreatingDeck = false;
        this.modals.createDeck = false
      },
      onSelectionChange(selectedRows) {
        this.selectedRows = selectedRows;
        if (selectedRows && selectedRows.length > 0) {
          this.deleteButtonDisabled = false
        } else {
          this.deleteButtonDisabled = true
        }
      },
      addToDeck(row) {
        this.modal = 'addToDeck'
      },
      async handleDeleteAll() {
        this.deleteButtonLoading = true;
        let promises = [];
        this.selectedRows.forEach((row, index) => {
          promises.push(this.deleteDeck(row.id))
        })
        await Promise.all(promises);
        this.deleteButtonLoading = false;
        this.deleteButtonDisabled = true;
      },
      handleResize() {
        this.isMobile = window.innerWidth < 768;
        this.isTablet = window.innerWidth < 1200;
      }
    },
    watch: {
      pagination () {
        this.getUserVocab();
      }
    }
  }
</script>

