<template>
  <div class="col-4 col-md-5 col-lg-3 offset-lg-2">
    <div class="row">
      <div class="col-12 col-md-auto">
        <base-button :type="pinyin ? 'default' : 'secondary'" size="sm" @click="changePinyin" :loading="loadingPinyin">{{ pinyin ? 'Hide Pinyin' : 'Show Pinyin' }}</base-button>
      </div>
      <div class="col-12 col-md-auto mt-1 mt-md-0">
        <base-button size="sm" :loading="loadingCharSet" @click="toggleCharSet()">{{charSet === 'simplified' ? 'Simp: ' : 'Trad: '}}</base-button>
      </div>
    </div>
  </div>
</template>

<script>
  import {mapState, mapMutations, mapActions} from 'vuex'
import {
  TOGGLE_PINYIN
} from '@/mutation-types'

export default {
  name: "Settings",
  data() {
    return {
      show: false,
      loadingCharSet: false,
      loadingPinyin: false
    }
  },
  computed: {
    ...mapState({
      pinyin: state => state.userInfo.pinyin,
      charSet: state => state.userInfo.charSet
    })
  },
  methods: {
    async toggleCharSet () {
      this.loadingCharSet = true;
      await this.changeCharSet(this.charSet === 'simplified' ? 'traditional' : 'simplified');
      this.loadingCharSet = false;
    },
    async changePinyin () {
      this.loadingPinyin = true;
      await this.togglePinyin(!this.pinyin)
      this.loadingPinyin = false;
    },
    ...mapActions({
      changeCharSet: 'changeCharSet',
      togglePinyin: 'togglePinyin'
    })
  }
}
</script>

