<template>
  <div>
    <tab-header title="Vocabulary" @print="print"/>
    <div class="pt-2">
      <vue-plyr v-show="showPlayer" ref="plyr" :options="playerOptions">
        <audio></audio>
      </vue-plyr>
      <base-table
        class=" col-12"
        :data="vocabSegments">
        <template slot="columns">
          <th class="text-center bg-transparent px-2" scope="col">
            {{ charSet }}
            <el-popover placement="top" trigger="hover" width="160" title="Stroke order">
              <i class="fa fa-info-circle" slot="reference"></i>
              <span class="text-left text-break">Click a phrase to start the animation</span>
            </el-popover>
          </th>
          <th class="text-center bg-transparent px-2" scope="col">Pinyin</th>
          <th class="text-center bg-transparent px-2" scope="col">English</th>
          <th class="text-center bg-transparent px-2" scope="col"></th>
        </template>
        <template slot-scope="{row, index}">
          <td class="text-center align-middle vocabulary px-1 px-lg-5">
            <character-animation :data="row[charSet.slice(0,1)]" :row-index="index" :key="row[charSet.slice(0,1)]"/>
          </td>
          <td class="text-center align-middle vocabulary px-1 px-lg-5 pinyin" v-html="row.p"></td>
          <td class="text-center align-middle vocabulary px-1 px-lg-5 english" v-html="row.en"></td>
          <td class="px-0 align-middle">
            <div class="row justify-content-end pr-md-3 pr-lg-4">
              <div class="col-12 col-md-3 col-auto mb-1">
                <base-button size="sm" type="secondary" @click.prevent="playVocab(row.audio)" class="btn-icon-only ml-1"><i class='fas fa-volume-up'></i></base-button>
              </div>
              <div class="col-12  col-md-3 col-auto mb-1">
                <base-button size="sm" type="secondary" @click.prevent="copyToClipboard(row)" class="btn-icon-only ml-1"><i class='far fa-copy'></i></base-button>
              </div>
              <div class="col-12  col-md-3 col-auto mb-1">
                <base-button size="sm" type="secondary" @click.prevent="addToVocabulary(index, row)" :loading="loading.vocab[index]" class="btn-icon-only ml-1" icon><i v-show="!loading.vocab[index]" class="fa fa-plus"></i></base-button>
              </div>
            </div>
          </td>
        </template>
      </base-table>
      <div class="row justify-content-end pt-4">
        <base-button type="secondary" size="sm" @click.prevent="addAllVocabToVocabulary()" :loading="loadingAddAllVocab"><i class="fa fa-plus"></i> Save All Vocabulary</base-button>
      </div>
    </div>
    <upgrade v-if="!premium && truncateData" hide-bullets  class="col-12 m-0 p-0 pt-2"  title="Examples have been shortened. Upgrade to Access All Premium Features."/>
    <div v-if="premium" id="lessonVocab" class="row d-none">
      <div class="col-3">
        <img src="@/assets/img/brand/brand.svg" class="img-fluid" alt="ChinesePod"/>
      </div>
      <h1 align="center" class="p-3">{{ lessonData.title }} - Vocabulary</h1>
      <base-table
        class="col-xs-12 col-md-10 offset-md-1 pt-5"
        :data="vocabSegments">
        <template slot="columns">
          <th class="text-center bg-transparent px-2" scope="col">
            {{ charSet.toUpperCase() }}
          </th>
          <th class="text-center bg-transparent px-2" scope="col">PINYIN</th>
          <th class="text-center bg-transparent px-2" scope="col">ENGLISH</th>
        </template>
        <template slot-scope="{row}">
          <td class="text-center align-middle vocabulary px-1 px-lg-5 chinese" v-html="row[charSet.slice(0,1)]"></td>
          <td class="text-center align-middle vocabulary px-1 px-lg-5 pinyin" v-html="row.p"></td>
          <td class="text-center align-middle vocabulary px-1 px-lg-5 english" v-html="row.en"></td>
        </template>
      </base-table>
    </div>
  </div>
</template>

<script>
  import BaseTable from "../BaseTable";
  import Upgrade from "./Upgrade";
  import CharacterAnimation from "./CharacterAnimation";
  import TabHeader from "./TabHeader";

  export default {
    name: "Vocab",
    components: {TabHeader, CharacterAnimation, Upgrade, BaseTable},
    props: {
      lessonId: String,
      lessonData: Object,
      vocabData: Array,
      extra: Boolean
    },
    computed: {
      access() {
        return this.$store.state.userInfo ? this.$store.state.userInfo.access : 'free'
      },
      premium () {
        return ['premium', 'admin'].includes(this.access)
      },
      charSet() {
        return this.$store.state.userInfo ? this.$store.state.userInfo.charSet : 'simplified'
      },
      player () { return this.$refs.plyr.player },
      vocabSegments () {
        return !this.truncateData ? this.vocabData : this.vocabData.slice(0,3)
      },
      upgradePath () {
        return this.$store.state.userInfo.upgrade.upgradePath
      },
      truncateData () {
        return !this.premium && this.upgradePath === 2
      }
    },
    data () {
      return {
        selectedVocab: {},
        showPlayer: false,
        wantsPrint: false,
        playerOptions: {
          controls: ['play-large', 'play', 'progress', 'current-time','settings'],
          settings: ['captions', 'quality', 'speed', 'loop', 'autoplay'],
          loop: { active: false }
        },
        loading: {
          vocab: {
            0: false,
            1: false,
            2: false,
            3: false,
            4: false,
            5: false,
            6: false,
            7: false,
            8: false,
            9: false,
            10: false,
            11: false,
            12: false,
            13: false,
            14: false,
            15: false,
            16: false,
            17: false,
            18: false,
            19: false,
            20: false,
            21: false,
            22: false,
            23: false,
            24: false,
            25: false,
            26: false,
            27: false,
            28: false,
            29: false,
            30: false,
            31: false,
            32: false,
            33: false,
            34: false,
            35: false,
            36: false,
            37: false,
            38: false,
            39: false,
            40: false
          }
        },
        loadingAddAllVocab: false
      }
    },
    methods: {
      playVocab(audio) {
        this.showPlayer = true;
        let source = audio.slice(0,4) === 'http'
          ? audio
          : process.env.VUE_APP_ROOT_S3 + (this.extra ? '/extra/' : '/') + this.lessonData.id + '/' + this.lessonData.hash_code + '/' + audio;
        this.player.source = {
          type: 'audio',
          title: 'Vocabulary',
          sources: [
            {
              src: source,
              type: 'audio/mp3'
            }
          ]
        };
        this.player.play();
      },
      copyToClipboard (row) {
        this.$copyText(`${row[this.charSet.slice(0,1)]} - ${row.p} - ${row.en}`)
          .then(() => {
            this.$notify({type: 'success', message: `Copied ${row[this.charSet.slice(0,1)]} to clipboard`});
          }, () => {
            this.$notify({type: 'danger', message: `Failed to save to clipboard`});
          })
      },
      print () {
        this.wantsPrint = true;
        this.$htmlToPaper('lessonVocab');
      },
      async addToVocabulary (index, row) {
        this.loading.vocab[index] = true;
        await this.axios.put(process.env.VUE_APP_ROOT_API + '/api/v1/vocabulary/words/add', {
          vocabularyId: row.id
        })
          .then(() => {
            this.$notify({type: 'success', message: `Added ${row[this.charSet.slice(0,1)]} to your vocabulary`});
          })
          .catch(() => {
            this.$notify({type: 'danger', message: `Failed to add to your vocabulary`});
          });
        this.loading.vocab[index] = false;
      },
      async addAllVocabToVocabulary () {
        this.loadingAddAllVocab = true;
        await this.axios.put(process.env.VUE_APP_ROOT_API + '/api/v1/vocabulary/words/add-many', {
          vocabularyId: this.vocabData.map(row => row.id)
        })
          .then(() => this.$notify({type: 'success', message: `Added all phrases to your vocabulary`}))
          .catch(() => this.$notify({type: 'danger', message: `Whoops! Something went wrong while saving your vocabulary`}));
        this.loadingAddAllVocab = false
      }
    }
  }
</script>

<style scoped>
  .vocabulary {
    word-break: break-word;
    flex-wrap: wrap !important;
    white-space: normal !important;
    /*padding: 0 !important;*/
  }
  .pinyin, .english {
    font-size: 20px;
  }
  .pinyin, .chinese {
    font-family: 'Noto Sans SC', sans-serif !important;
  }
</style>
