<template>
  <div class="upgrade-wrapper">
    <vue-plyr ref="plyr" :options="playerOptions" :emit="['pause', 'ended', 'play']" @play="started = true" @pause="play()" @ended="end()">
      <div class="plyr__video-embed">
        <iframe
          :src="`https://player.vimeo.com/video/${videoId}?loop=false&byline=false&portrait=false&title=false&speed=true&transparent=1&gesture=media&autoplay=1`"
          autoplay allow="autoplay">
        </iframe>
      </div>
    </vue-plyr>
    <a href="#" @click="upgradeClick" class="upgrade-text">
      Want to enjoy ChinesePod without ads?<br>
      Click <strong><u>here</u></strong> to upgrade to Premium!
    </a>
  </div>
</template>

<script>
  export default {
    name: 'ContentAds',
    components: {  },
    props: {
      videoId: {
        type: String,
        required: true
      },
      videoLength: {
        type: Number,
        default: 15
      }
    },
    computed: {
      player () {
        return this.$refs.plyr.player
      },
      upgradeLink () {
        return this.$store.state.userInfo.upgrade.upgradeLink
      }
    },
    data() {
      return {
        started: false,
        playerOptions: {
          controls: [],
          settings: [],
          loop: {active: false},
          fullscreen: {enabled: false}
        }
      }
    },
    methods: {
      pause(video) {
        const elements = document.getElementsByClassName('w-play-pause-notifier');
        for (let element of elements) {
          element.style.display = 'none'
        }
        video.play()
      },
      play () {
        this.player.play()
      },
      end() {
        this.$emit('end')
      },
      async upgradeClick () {
        await this.$store.dispatch('logEvent', {
          action: 'click_preroll_ad',
          'event_category': 'advertising',
          'event_label': `Clicked Preroll Ad ${this.videoId}`
        });
        window.location.href = this.upgradeLink
      }
    },
    mounted () {
      setTimeout(() => {
        if (!this.started) {
          this.$emit('end')
        }
      }, this.videoLength * 1000);
      this.$emit('play')
    }
  }
</script>
