<template>
  <div @click="animate" :aria-label="data">
    <span v-for="(character, i) in data" :key="i" :id="`${rowIndex}-${i}-${character}`" class="characters">{{ isPunctuation(character) ? character : '' }}</span>
  </div>
</template>

<script>
  import HanziWriter from 'hanzi-writer';
  import bugsnagClient from "@/lib/bugsnag";

  export default {
    name: "CharacterAnimation",
    props: {
      autoplay: {
        type: Boolean,
        default: false
      },
      data: String,
      rowIndex: Number,
      size: {
        type: Number,
        default: 40
      }
    },
    data() {
      return {
        animations: []
      }
    },
    methods: {
      asyncForEach: async (array, callback) => {
        for (let index = 0; index < array.length; index++) {
          await callback(array[index], index, array)
        }
      },
      isPunctuation(character) {
        return new RegExp(`[!"\#$%&'()*+,\-./:;<=>?@\[\\\]^_\`{|}~]|\\s|[A-z]|[0-9]|\\d+`).test(character);
      },
      async animate () {
        try {
          await this.asyncForEach(this.animations, (character) => character.hideCharacter({duration: 0}));
          await this.asyncForEach(this.animations, async (character) => {
            await character.animateCharacter({onComplete: async function() {}})
          });
        } catch (e) {

        }
      }
    },
    async mounted () {
      try {
        this.data.split('').forEach((character, index) => {
          if(!this.isPunctuation(character)){
            try {
              this.animations.push(HanziWriter.create(`${this.rowIndex}-${index}-${character}`, character, {
                width: this.size,
                height: this.size,
                padding: 0,
                showOutline: true,
                onLoadCharDataError: () => {
                  document.getElementById(`${this.rowIndex}-${index}-${character}`).innerHTML = character;
                  bugsnagClient.notify(`Oh No! Could not load character data - ${character}`);
                }
              }))
            } catch (e) {
              bugsnagClient.notify(e);
            }
          }
        });

        if (this.autoplay) {
          await this.animate();
        }

      } catch (e) {
        bugsnagClient.notify(e);
      }

    }
  }
</script>

<style lang="scss" scoped>
  .characters {
    font-size: 30px;
  }

</style>
