<template>
  <card class="pb-5">
    <!-- Card header -->
    <h3 slot="header" class="mb-0">Connect Your Device</h3>

    <!-- Card body -->
    <div class="row">
      <div class="col-lg-8">
        <p class="mb-0">
          Take your learning to the next level and connect your account to one of <strong>ChinesePod Mobile Apps</strong>.
        </p>
      </div>
    </div>
    <hr>
    <div v-show="success">
      <base-alert type="success" dismissible>
        <span class="alert-inner--text"><strong>Successfully connected!</strong>
          You can now close this window and return to the application</span>
        <button type="button" class="close" data-dismiss="alert" aria-label="Close">
          <span aria-hidden="true">&times;</span>
        </button>
      </base-alert>
      <router-link to="/home">
        <base-button type="primary">Back to Dashboard</base-button>
      </router-link>
    </div>
    <form class="col-12 col-md-8 offset-md-2 col-lg-6 offset-lg-3 col-xl-4 offset-xl-4" v-show="!success" @submit.prevent="submitConnectDevice()">
        <base-input label="Code shown on your device" prepend-icon="fas fa-mobile-alt" placeholder="ABCD12" name="Device Code" type="text" v-model="code" :error="errors.code" autofocus aria-required="true" required></base-input>
      <div class="text-center">
        <base-button type="success" :loading="loading" native-type="submit">Connect Device</base-button>
      </div>
    </form>
  </card>
</template>
<script>
  import {mapState} from 'vuex';
  import bugsnagClient from '@/lib/bugsnag'

  export default {
    computed: {
      ...mapState({
        userInfo: 'userInfo'
      }),
      mobileDevice () {
        return window.innerWidth < 768
      }
    },
    title() {
      return 'Connect Your Device | ChinesePod'
    },
    data() {
      return {
        validated: false,
        success: false,
        loading: false,
        code: '',
        errors: {
          code: ''
        }
      }
    },
    methods: {
      async submitConnectDevice () {
        this.loading = true;
        await this.axios.post(process.env.VUE_APP_ROOT_API + '/api/v1/device/code', {code: this.code})
          .then(() => {
            this.loading = false;
            this.success = true;
            this.$notify({type: 'info', message: `Your device has been connected.`});
          })
          .catch((e) => {
            this.loading = false;
            bugsnagClient.metaData = {formData: this.feedback};
            bugsnagClient.notify(e);
            this.errors.code = 'Sorry, this device code is invalid. Please check the information and try again.'
            this.$notify({type: 'danger',
              message: `Sorry, this device code is invalid.
              Please check the information and try again.
              If you're still experiencing this issue, please reach out to our team at support@chinesepod.com.`,
              timeout: 10000});
          })
      },
      getError(name){
        return this.errors.first(name)
      },
      isValid(name) {
        return this.validated && !this.errors.has(name);
      }
    }
  }
</script>
