<template>

  <router-link :to="{ name: isPlaylist ? 'playlist' : 'course', params: {courseId: course.id}}" :title="course.course_title">
  <div class="card bg-transparent border-0 mb-0" :class="hideButtons ? 'mb-0 pb-0' : 'mb-5'">
      <div v-lazy-container="{ selector: 'img' }">
        <img
          class="card-img"
          :data-src="`https://chinesepod.com/images/courses/${course.id}/course.svg`"
          :data-loading="preloaderSquare"
          :data-error="preloaderSquare"
          :alt="course.course_title">
      </div>
      <div v-show="!hideButtons" class="card-img-overlay rounded shadow-none" :class="[course.enrolled ? 'bg-translucent-dark' : 'bg-transparent']">
        <div class="float-right">
          <button
            class="btn btn-secondary btn-circle btn-circle-sm mt--4 mr--3"
            :class="[course.enrolled ? 'text-danger' : '']"
            @click.prevent="enrollCourse(course)"
          ><i class="fas" :class="[loading ? 'fa-spinner fa-spin' : course.enrolled ? 'fa-minus' : 'fa-plus']"></i></button>
        </div>
      </div>
  </div>
  </router-link>
</template>

<script>
  import preloaderSquare from '@/assets/img/preloader-square.svg'

  export default {
    name: "CourseCard",
    props: {
      course: {
        type: Object,
        description: "Course data to show"
      },
      hideButtons: {
        type: Boolean,
        defaultsTo: false
      },
      isPlaylist: {
        type: Boolean,
        defaultsTo: false
      }
    },
    data () {
      return {
        preloaderSquare: preloaderSquare,
        loading: false
      }
    },
    methods: {
      enrollCourse: async function (course) {
        this.loading = true;
        course.enrolled = !course.enrolled;
        await this.$http.post(process.env.VUE_APP_ROOT_API + '/api/v1/dashboard/toggle-course', {
          courseId: course.id,
          status: course.enrolled
        })
          .then(() => {
            this.$store.dispatch('getUserCourses');
            if (course.enrolled) {
              this.$notify({type: 'success', message: `Added course: ${ course.course_title }`});
            } else {
              this.$notify({type: 'warning', message: `Removed course: ${ course.course_title }`});
            }
          })
          .catch(() => {
            this.$notify({type: 'danger', message: `Sorry, something went wrong.
            Please wait a moment and try again. If you're still experiencing this issue, please reach out to our team at support@chinesepod.com.`});
          });
        this.loading = false;
        this.$forceUpdate();
        this.$emit('addedcourse');
      }
    }
  }
</script>

