<template>
  <div>
    <base-button v-if="premium"  class="d-block float-right" size="sm" @click="$emit('print')"><i class="fas fa-print"></i> Print</base-button>
    <base-button class="float-right" size="sm" :loading="loadingCharSet" @click="toggleCharSet">{{charSet === 'simplified' ? 'Simp: ' : 'Trad: '}}</base-button>
    <h2 class="h2">{{title}}</h2>
  </div>
</template>

<script>
  import {mapActions, mapState} from "vuex";

  export default {
    name: "TabHeader",
    props: {
      title: String
    },
    data () {
      return {
        loadingCharSet: false,
        loadingPinyin: false
      }
    },
    computed: {
      access() {
        return this.$store.state.userInfo ? this.$store.state.userInfo.access : 'free'
      },
      premium () {
        return ['premium', 'admin'].includes(this.access)
      },
      audioRoot() {
        return process.env.VUE_APP_ROOT_S3 + (this.lessonData.extra ? '/extra/' : '/') +`${this.lessonData.id}/${this.lessonData.hash_code}`
      },
      expansionSegments () {
        return !this.truncateData ? this.expansionData : this.expansionData.slice(0,1)
      },
      truncateData () {
        return !this.premium && this.upgradePath === 2
      },
      ...mapState({
        pinyin: state => state.userInfo.pinyin,
        charSet: state => state.userInfo.charSet
      })
    },
    methods: {
      async toggleCharSet () {
        this.loadingCharSet = true;
        await this.changeCharSet(this.charSet === 'simplified' ? 'traditional' : 'simplified');
        this.loadingCharSet = false;
      },
      async changePinyin () {
        this.loadingPinyin = true;
        await this.togglePinyin(!this.pinyin)
        this.loadingPinyin = false;
      },
      ...mapActions({
        changeCharSet: 'changeCharSet',
        togglePinyin: 'togglePinyin'
      })
    }
  }
</script>

