<template>
  <div
    :class="['wistia_embed', 'lesson-media-content', 'wistia_async_' + videoId]"
  ></div>
</template>

<script>
  export default {
    name: 'WistiaPlayer',
    props: {
      videoId: {
        type: String,
        required: true
      },
      options: {
        type: Object,
        default: () => ({})
      }
    },
    data() {
      return {
        video: null
      }
    },
    mounted() {
      if (!window._wq) {
        let wistiaScript = document.createElement('script');
        wistiaScript.setAttribute('src', 'https://fast.wistia.net/assets/external/E-v1.js');
        document.head.appendChild(wistiaScript);
        window._wq = window._wq || [];
      }
      this.play()
    },
    methods: {
      updateProgress(video) {
        this.$emit('progress', Math.ceil(video.percentWatched() * 100))
      },
      play() {
        window._wq = window._wq || [];
        _wq.push({
          id: this.videoId,
          options: {
            wmode: 'transparent',
            autoPlay: true,
            ...this.options
          },
          onReady: video => {
            video.unmute();
            video.volume(1);

            this.video = video;
            this.$emit('play', video);

            video.bind('pause', () => {
              this.updateProgress(video);
              this.$emit('pause', video)
            });

            video.bind('end', () => {
              this.updateProgress(video);
              this.$emit('end', video)
            })
          }
        })
      }
    }
  }
</script>
<style lang="scss">
  .lesson-media-content {
    position: relative;
    width: 100%;
    height: 0;
    padding-bottom: 56.25%;
    & > div {
      position: absolute !important;
      width: 100% !important;
      height: 100% !important;
      left: 0;
      top: 0;
    }
  }

</style>
