                          <div v-if="row.tags" >
                            <div v-for="(tag, index) in row.tags" :key="index">
                              <router-link v-if="tag && tag.id" :to="{name: 'decks-details', params: {deckId: tag.id}}"  class="text-break">{{ tag && tag.tag ? tag.tag : '' }}</router-link>
                            </div>
