                        <template slot-scope="{$index, row}">
                          <div>
                            {{ new Date(row.createdAt).toLocaleDateString('en-US', { year: 'numeric', month: 'short', day: '2-digit' }) }}
                          </div>
                        </template>
                      </el-table-column>
                      <el-table-column min-width="180px"
                                       align="right"
