                      </el-table-column>
                      <el-table-column type="expand"
                                       label="Expand"
                                       v-if ="isMobile || isTablet"
                                       width="130px">
                        <template slot-scope="{row}">
                          <div class="col-12 col-md-8 offset-md-2">
                            <p class="vocabulary chinese" v-html="row.s"></p>
                            <p v-if="row.s !== row.t" class="vocabulary chinese" v-html="row.t"></p>
                            <p class="vocabulary pinyin" v-html="row.p"></p>
                            <div v-if="multipleDefinitions(row.en)" class="vocabulary english">
                              <ol class="list-inline vocabulary english">
                              <li class="list-inline-item" v-for="(definition, index) in cleanDefinitions(row.en)" :key="index">
                                <div v-if="definition instanceof Object">
                                  <strong>{{ index + 1}}</strong> <strong class="small font-weight-bold">{{ definition.type }}:</strong> <router-link :to="`/dictionary/${definition[charSet]}`" :title="definition[charSet]">{{definition[charSet]}} {{ cleanPinyin(definition.pinyin) }}</router-link>
                                </div>
                                <div v-else>
