                                </div>
                              </li>
                            </ol>
                            </div>
                            <p v-else class="vocabulary english" v-html="row.en"></p>
                            <div v-if="row.tags" >
                              <p class="pb-0 mb-0 text-wrap">
                                <small class="vocabulary">Decks: </small>
                                <a v-for="(tag, index) in row.tags" :key="index">
                                  <router-link v-if="tag && tag.id" :to="{name: 'decks-details', params: {deckId: tag.id}}" class="text-break">{{ tag && tag.tag ? tag.tag : '' }}</router-link>{{ (row.tags.length - 1) > index ? ', ' : '' }}
                                </a>
                              </p>
                              <base-button
                                @click.native="addToDeck(row)"
                                class="btn-icon-only"
                                type="default"
                                size="sm"
                                icon
                              >
                                <i class="text-white fa fa-plus"></i>
                              </base-button>
                              <base-button
                                v-if="row.tags && row.tags.length"
                                @click.native="removeFromDeck(row)"
                                class="btn-icon-only"
                                type="default"
                                size="sm"
                                icon
                              >
                                <i class="text-white fa fa-minus"></i>
                              </base-button>
                            </div>
                            <p>
                              <small class="vocabulary">
                                Added: {{ new Date(row.createdAt).toLocaleDateString('en-US', { year: 'numeric', month: 'short', day: '2-digit' }) }}
                              </small>
                            </p>
                            <div>
                              <base-button
                                v-if="row.audioUrlCN"
                                class="play btn-link btn-icon-only"
                                type="secondary"
                                size="sm"
                                icon
                                @click="playVocab(row.audioUrlCN, row.lesson)"
                              >
                                <i class="fa fa-play"></i>
                              </base-button>
                              <base-button
                                class="search btn-link btn-icon-only"
                                type="info"
                                size="sm"
                                icon
                                @click="$router.push({ path: 'explore', query: { search: row[charSet.slice(0,1)] }})"
                              >
                                <i class="text-white fa fa-search"></i>
                              </base-button>
                              <base-button
                                class="copy btn-link btn-icon-only "
                                type="secondary"
                                size="sm"
                                icon
                                @click="copyToClipboard(row)"
                              >
                                <i class="far fa-copy"></i>
                              </base-button>
                              <delete-button :row="row" :character="row[charSet.slice(0,1)]" @deleted="handleDeleted($index,row)"/>
