      <base-input alternative
                  class="mb-3"
                  label="Deck name"
                  placeholder="Name"
                  v-model="deckInfo.tag"
                  @keyup.enter="updateDeck"
                  addon-left-icon="ni ni-email-83">
      </base-input>
      <base-button type="danger" icon size="sm" @click="modals.deleteDeck = true" class="mb-1 float-right">
        <span class="btn-inner--icon"><i class="fas fa-trash"></i></span>
        <span class="btn-inner--text">Delete Deck</span>
      </base-button>
      <template slot="footer">
        <base-button type="primary" @click.prevent="updateDeck" :loading="loadingSavingDeck">Save changes</base-button>
        <base-button type="link" class="ml-auto" @click="modals.editDeck = false">Close
        </base-button>
      </template>
    </modal>
    <modal :show.sync="modals.addToDeck">
      <h6 slot="header" class="modal-title">Add to deck</h6>
      <el-select class="select-danger col-9 pl-0"
                 placeholder="Select Deck"
                 :disabled="selected.new"
                 v-model="selected.deck">
        <el-option v-for="option in userDecks"
                   class="select-danger"
                   :value="option.id"
                   :label="option.tag"
                   :key="option.id">
        </el-option>
      </el-select>
      <base-button
        v-if="!selected.new"
        type="success"
        size="sm"
        class="btn-icon-only"
        @click.prevent="selected.new = true"
        :disabled="loadingAddingToDeck">
        <i class="fa fa-plus"></i>
      </base-button>
      <base-button
        v-else
        size="sm"
        class="btn-icon-only"
        type="danger"
        @click.prevent="selected.new = false">
        <i class="fa fa-trash"></i>
      </base-button>
      <base-input
        v-if="selected.new"
        alternative
        class="mb-3"
        label="Deck name"
        placeholder="Name"
        v-model="deckInfo.tag"
        @keyup.enter="addPhraseToDeck"
        addon-left-icon="ni ni-email-83">
      </base-input>
      <template slot="footer">
        <base-button type="success" @click.prevent="addPhraseToDeck" :loading="loadingAddingToDeck">Add to deck</base-button>
        <base-button type="link" @click="modals.addToDeck = false" class="ml-auto" >Close
        </base-button>
      </template>
    </modal>
    <modal :show.sync="modals.deleteDeck">
      <h6 slot="header" class="modal-title">Delete deck</h6>
      <p>This will only delete the deck and keep your vocabulary as Uncategorized.</p>
      <template slot="footer">
        <base-button type="danger" @click.prevent="deleteDeck" :loading="loadingDeletingDeck">Delete</base-button>
        <base-button type="link" class="ml-auto" @click="modals.deleteDeck = false">Close
        </base-button>
      </template>
    </modal>
    <modal :show.sync="modals.removeFromDeck">
      <h6 slot="header" class="modal-title">Remove from deck</h6>
      <el-select class="select-danger"
                 placeholder="Select Deck"
                 v-model="selected.deck">
        <el-option v-for="option in userDecks"
                   class="select-danger"
                   :value="option.id"
                   :label="option.tag"
                   :key="option.id">
        </el-option>
      </el-select>
      <template slot="footer">
        <base-button type="warning" @click.prevent="removePhraseFromDeck" :loading="loadingRemovingFromDeck">Remove from deck</base-button>
        <base-button type="link" @click="modals.removeFromDeck = false" class="ml-auto" >Close
        </base-button>
      </template>
    </modal>
    <modal :show.sync="modals.removeOneFromDeck">
      <h6 slot="header" class="modal-title">Remove from deck</h6>
      <el-select class="select-danger"
                 placeholder="Select Deck"
                 v-model="selected.deck">
        <el-option v-for="option in relevantDecks"
                   class="select-danger"
                   :value="option.id"
                   :label="option.tag"
                   :key="option.id">
        </el-option>
      </el-select>
      <template slot="footer">
        <base-button type="warning" @click.prevent="removePhraseFromDeck" :loading="loadingRemovingFromDeck">Remove from deck</base-button>
        <base-button type="link" @click="modals.removeOneFromDeck = false" class="ml-auto" >Close
        </base-button>
      </template>
    </modal>
  </div>
</template>

<script>
  import {mapState} from 'vuex'
  import {cleanDefinitions, cleanPinyin, multipleDefinitions} from "@/util/dictionary";
  import CharacterAnimation from "../../components/Lesson/CharacterAnimation";
  import { BasePagination } from '@/components';
  import clientPaginationMixin from '@/views/Tables/PaginatedTables/clientPaginationMixin'
  import { Table, TableColumn, DropdownMenu, DropdownItem, Dropdown, Select, Option } from 'element-ui'
  import DeleteButton from "./DeleteButton";

  export default {
    name: "DecksIndex",
    mixins: [clientPaginationMixin],
    components: {
      DeleteButton,
      BasePagination,
      CharacterAnimation,
      [Table.name]: Table,
      [TableColumn.name]: TableColumn,
      [Select.name]: Select,
      [Option.name]: Option,
      [Dropdown.name]: Dropdown,
      [DropdownItem.name]: DropdownItem,
      [DropdownMenu.name]: DropdownMenu
    },
    data () {
      return {
        deckId: '',
        loading: true,
        loadingDelete: {},
        propsToSearch: ['s', 't', 'p', 'en'],
        tableData: [],
        userDecks: [],
        relevantDecks: [],
        showPlayer: false,
        wantsPrint: false,
        selectedRows: [],
        selected: {
          deck: '',
          phrase: '',
          new: false
        },
        deckInfo: {},
        playerOptions: {
          controls: ['play-large', 'play', 'progress', 'current-time','settings'],
          settings: ['captions', 'quality', 'speed', 'loop', 'autoplay'],
          loop: { active: false }
        },
        modals: {
          addToDeck: false,
          editDeck: false,
          deleteDeck: false,
          createDeck: false,
          removeFromDeck: false,
          removeOneFromDeck: false
        },
        deleteButtonLoading: false,
        deleteButtonDisabled: true,
        addButtonLoading: false,
        addButtonDisabled: true,
        loadingSavingDeck: false,
        loadingDeletingDeck: false,
        loadingAddingToDeck: false,
        loadingRemovingFromDeck: false,
        removeButtonLoading: false,
        isMobile: false,
        isTablet: false
      }
    },
    computed: {
      premium () {
        return ['premium', 'admin'].includes(this.access)
      },
      player () { return this.$refs.plyr.player },
      ...mapState({
        charSet: state => state.userInfo.charSet,
        pinyin: state => state.userInfo.pinyin,
        access: state => state.userInfo.access
      })
    },
    created() {
      window.addEventListener('resize', this.handleResize);
      this.handleResize();
    },
    destroyed() {
      window.removeEventListener('resize', this.handleResize);
    },
    beforeRouteUpdate(to, from, next) {
      this.deckId = to.params.deckId;
      next();
      this.getUserDeck();
      this.axios.get(process.env.VUE_APP_ROOT_API + '/api/v1/vocabulary/decks')
        .then(({data}) => {
          this.userDecks = data;
        })
        .catch(() => {});
    },
    mounted () {
      this.deckId = this.$route.params.deckId
      this.getUserDeck();
      this.axios.get(process.env.VUE_APP_ROOT_API + '/api/v1/vocabulary/decks')
        .then(({data}) => {
          this.userDecks = data;
        })
        .catch(() => {})
    },
    methods: {
      cleanPinyin: cleanPinyin,
      cleanDefinitions: cleanDefinitions,
      multipleDefinitions: multipleDefinitions,
      async getUserDeck () {
        this.loading = true;
        await this.axios.get(process.env.VUE_APP_ROOT_API + '/api/v1/vocabulary/decks/' + this.deckId)
          .then(({data}) => {
            this.deckInfo = data
            this.tableData = data.vocab;
          })
          .catch(() => {});
        this.loading = false;
      },
      async deleteDeck() {
        this.loadingDeletingDeck = true;
        await this.axios.delete(process.env.VUE_APP_ROOT_API + '/api/v1/vocabulary/decks/' + this.deckId )
          .then(() => this.$router.push({name: 'decks'}))
          .catch();
        this.loadingDeletingDeck = false;
      },
      async updateDeck() {
        this.loadingSavingDeck = true;
        await this.axios.post(process.env.VUE_APP_ROOT_API + '/api/v1/vocabulary/decks/' + this.deckId, {
          tag: this.deckInfo.tag
        })
          .then(({data}) => {
            this.deckInfo = data
          })
          .catch(() => {});
        this.loadingSavingDeck = false;
        this.modals.editDeck = false;
      },
      copyToClipboard (row) {
        this.$copyText(`${row[this.charSet.slice(0,1)]} - ${row.p} - ${row.en}`)
          .then(() => {
            this.$notify({type: 'success', message: `Copied ${row[this.charSet.slice(0,1)]} to clipboard`});
          }, () => {
            this.$notify({type: 'danger', message: `Failed to save to clipboard`});
          })
      },
      onSelectionChange(selectedRows) {
        this.selectedRows = selectedRows;
        this.deleteButtonDisabled = !(selectedRows && selectedRows.length > 0);
        this.addButtonDisabled = !(selectedRows && selectedRows.length > 0);
      },
      async addPhraseToDeck () {
        this.loadingAddingToDeck = true
        let promises = [
          this.axios.get(process.env.VUE_APP_ROOT_API + '/api/v1/vocabulary/decks/' + this.deckId)
            .then(({data}) => {
              this.deckInfo = data;
              this.tableData = data.vocab;
            })
            .catch(() => {})]
        if(this.selected.new) {
          await this.axios.put(process.env.VUE_APP_ROOT_API + '/api/v1/vocabulary/decks', {
            tag: this.deckInfo.tag
          })
            .then(({data}) => {
              this.userDecks.push(data)
              this.selected.deck = data.id})
            .catch()
          promises.push(this.axios.get(process.env.VUE_APP_ROOT_API + '/api/v1/vocabulary/decks')
            .then(({data}) => {this.userDecks = data;}).catch(() => {}));
          this.deckInfo.tag = '';
          this.selected.new = false;
        }
        if(!this.selected.deck || !this.selected.phrase) {
          return
        }
        await this.axios.put(process.env.VUE_APP_ROOT_API + '/api/v1/vocabulary/decks/add', {
          deckId: this.selected.deck,
          wordId: this.selected.phrase
        }).catch()
        await Promise.all(promises);
        this.loadingAddingToDeck = false;
        this.modals.addToDeck = false;
      },
      async removePhraseFromDeck () {
        this.loadingRemovingFromDeck = true
        if(!this.selected.deck || !this.selected.phrase) {
          return
        }
        await this.axios.post(process.env.VUE_APP_ROOT_API + '/api/v1/vocabulary/decks/remove', {
          deckId: this.selected.deck,
          wordId: this.selected.phrase
        }).catch();
        await this.axios.get(process.env.VUE_APP_ROOT_API + '/api/v1/vocabulary/decks/' + this.deckId)
          .then(({data}) => {
            this.deckInfo = data;
            this.tableData = data.vocab;
          })
          .catch(() => {});
        this.loadingRemovingFromDeck = false;
        this.modals.removeFromDeck = false;
        this.modals.removeOneFromDeck = false;
      },
      addToDeck(row) {
        this.modals.addToDeck = true;
        this.selected.phrase = [row.user_vocabulary_id]
      },
      addAllToDeck() {
        this.modals.addToDeck = true;
        this.selected.phrase = this.selectedRows.map(row => row.user_vocabulary_id)
      },
      removeFromDeck(row) {
        this.modals.removeOneFromDeck = true;
        this.selected.phrase = [row.user_vocabulary_id];
        this.relevantDecks = row.tags;
        this.selected.deck = this.relevantDecks[0].id
        // this.removableDecks = row.tags
      },
      removeAllFromDeck() {
        this.modals.removeFromDeck = true;
        this.selected.phrase = this.selectedRows.map(row => row.user_vocabulary_id);
        // let relevantDecks = [];
        // this.selectedRows.forEach(row => row.tags.forEach(tag => relevantDecks.push(tag)));
        // console.log(relevantDecks);
        // console.log(new Set(relevantDecks));
        // this.removableDecks = [...new Set(relevantDecks)]
      },
      async createDeck() {
        this.loadingCreatingDeck = true
        await this.axios.put(process.env.VUE_APP_ROOT_API + '/api/v1/vocabulary/decks', {
          tag: this.deckInfo.tag
        })
          .then(() => this.getUserDecks())
          .catch()
        this.loadingCreatingDeck = false;
        this.modals.createDeck = false
      },
      async handleDelete(row) {
        await this.axios.delete(process.env.VUE_APP_ROOT_API + '/api/v1/vocabulary/words/' + row.user_vocabulary_id )
          .catch()
      },
      async handleDeleteOne(index, row) {
        this.queriedData[index].loading = true;
        await this.handleDelete(row)
          .then(() => {this.tableData = this.tableData.filter(word => word.user_vocabulary_id !== row.user_vocabulary_id)});
        this.queriedData[index].loading = false
      },
      async handleDeleted(index, row) {
        this.tableData = this.tableData.filter(word => word.user_vocabulary_id !== row.user_vocabulary_id)
      },
      async handleDeleteAll() {
        this.deleteButtonLoading = true;
        await this.axios.post(process.env.VUE_APP_ROOT_API + '/api/v1/vocabulary/words/delete-many', {
          wordId: this.selectedRows.map(row => row.user_vocabulary_id)
        }).catch();
        this.deleteButtonLoading = false;
        this.deleteButtonDisabled = true;
        await this.getUserDeck();
      },
      handleResize() {
        this.isMobile = window.innerWidth < 768;
        this.isTablet = window.innerWidth < 1200;
      },
      playVocab(audio, lesson) {
        this.showPlayer = true;
        let source = audio.slice(0,4) === 'http'
          ? audio
          : process.env.VUE_APP_ROOT_S3 + (lesson.type === 'extra' ? '/extra/' : '/') + lesson.id + '/' + lesson.hash_code + '/' + audio;
        this.player.source = {
          type: 'audio',
          title: 'Vocabulary',
          sources: [
            {
              src: source,
              type: 'audio/mp3'
            }
          ]
        };
        this.player.play();
      }
    },
    watch: {
      pagination () {
        this.getUserVocab();
      }
    }
  }
</script>

