<template>
  <base-button type="primary" size="lg" class="text-center text-capitalize btn-save d-inline-block" :loading="loading" @click="addWord">
    <div v-if="!loading">
      <div class="col-12 p-0">
        <i class="fa mr-0 save fas fa-plus"/>
      </div>
      <div class="col-12 p-0">
        {{ loading ? 'Adding' : 'Save'}}
      </div>
    </div>
  </base-button>
</template>

<script>

  import {mapState} from 'vuex'

  export default {
    name: 'dictionary-add-button',
    props: {
      word: Object,
      square: {
        type: Boolean,
        default: false
      }
    },
    computed: {
      ...mapState({
        charSet: state => state.userInfo.charSet
      })
    },
    data() {
      return {
        loading: false
      }
    },
    methods: {
      async addWord () {
        this.loading = true;
        await this.axios.put(process.env.VUE_APP_ROOT_API + '/api/v1/vocabulary/words/create', {
          s: this.word.simplified,
          t: this.word.traditional,
          p: this.word.pinyin,
          en: this.word.items.join('/'),
          audio: this.word.audioUrl
        })
          .then(() => {
            this.$notify({type: 'success', message: `Added ${this.word[this.charSet]} - ${this.word.pinyin} to your vocabulary`});
          })
          .catch(() => {
            this.$notify({type: 'danger', message: `Failed to add to your vocabulary`});
          });
        this.loading = false;
      }
    }
  }
</script>

<style lang="scss" scoped>
  .save {
    font-size: 30px;
    @media (max-width: 576px)  {
      font-size: 20px;
      padding-right: .75em;
    }
  }
  .btn-save {
    width: 78px;
    height: 78px;
    padding-top: 10px;
    padding-bottom: 10px;
    @media (max-width: 576px)  {
      width: 60px;
      height: 60px;
      padding: 4px 0;
    }

  }
</style>
